import Authorization from "../user/auth.service";

const HotelRoomPriceService = {        
    async searchRoomPrices(params) {
        try {
            params.auth = 'bearer'
            const { status, data } = await Authorization.checkLogin('/hotel-room-price', params, 'post')
            if (status == 200) {
                const res = []     
                if(data)           
                    for(let rp of data) {                    
                        res.push({
                            id: rp.id,
                            tarifa_id: rp.rate_type.id,
                            tarifa: rp.rate_type.name,
                            fecha: rp.date,
                            SGL: rp.single_price,
                            DBL: rp.double_price,
                            TRP: rp.triple_price,
                            '1er-Nio': rp.first_child_price,
                            '2do-Nio': rp.second_child_price,
                            'Nio + Adulto': rp.first_child_one_adult_price,
                            '2 Nio + Adulto': rp.second_child_one_adult_price,
                            'Fecha Reserva Ini': (rp.bookingw_ini) ? rp.bookingw_ini : '0000-00-00',
                            'Fecha Reserva End': (rp.bookingw_end) ? rp.bookingw_end : '0000-00-00',
                            margen: rp.upcount,
                            'en venta': (rp.stop_sale) ? false : true,
                        })
                    }                
                return res
            }
        } catch (e) {
            //! Fail             
            console.log(e.response)
        }
        return false
    },
    addPrices(params) {
        return this.actionQuery('/hotel-room-price/create', params).then(e => {return e})        
    },
    async deletePrices(params) {
        return this.actionQuery('/hotel-room-price/delete', params).then(e => {return e})
    },
    async actionQuery(url, params) {
        try {
            params.auth = 'bearer'
            const { status, data } = await Authorization.checkLogin(url, params, 'post')
            if (status == 200) {
                return this.searchRoomPrices(params).then(res => {return res})
                // return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    }
}

export default HotelRoomPriceService