import Authorization from "../user/auth.service";

const base_url = 'car-price'
const CarPriceService = {        
    async searchCarPrices(params) {
        try {
            params.auth = 'bearer'
            const { status, data } = await Authorization.checkLogin(`/${base_url}`, params, 'post')
            if (status == 200) {
                const res = []     
                if(data)           
                    for(let rp of data) {  
                        res.push({
                            id: rp.id,
                            car: rp.car.alias,
                            tarifa_id: rp.rate_type.id,
                            tarifa: rp.rate_type.name,
                            fecha: rp.date,
                            'precio 3 a 6': rp.price_3a6,
                            'precio 7 a 30': rp.price_7a30,
                            'precio mas de 30': rp.price_mas30,
                            margen: rp.upcount,
                            'en venta': (rp.stop_sale) ? false : true,
                        })
                    }                
                return res
            }
        } catch (e) {
            //! Fail             
            console.log(e.response)
        }
        return false
    },
    addPrices(params) {
        return this.actionQuery(`/${base_url}/create`, params).then(e => {return e})        
    },
    async deletePrices(params) {
        return this.actionQuery(`/${base_url}/delete`, params).then(e => {return e})
    },
    async actionQuery(url, params) {
        try {
            params.auth = 'bearer'
            const { status, data } = await Authorization.checkLogin(url, params, 'post')
            if (status == 200) {  
                return this.searchCarPrices(params).then(res => {return res})
                // return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    }
}

export default CarPriceService