                                </div>                            
                            </div>
                        </template>  
                        <!-- Car Info -->
                        <template v-if="bk_item.type === 'Car'"
                            class="border border-gray-400 rounded-md mb-5">
                                    
                            <div class="grid grid-cols-2 gap-3 mx-2 my-3">
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">fecha de llegada</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.check_in_date }}</span>                                
                                </div>
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">fecha de salida</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.check_out_date }}</span>                                
                                </div>
                            </div>

                            <div class="grid grid-cols-2 gap-3 mx-2 my-3">
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Categoria</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.car }}</span>                                
                                </div>                            
                                <div>
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">Precio Total</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.total_price }}</span>                                
                                </div>
                            </div>
                                                
                        </template> 
                        <!-- Marketplace Info -->
                        <template v-if="bk_item.type === 'MarketItem'"
                            class="border border-gray-400 rounded-md mb-5">
                                    
                            <div class="grid grid-cols-2 gap-3 mx-2 my-3">
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">fecha</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.date }}</span>                                
                                </div>
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Cantidad</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.cant }}</span>                                
                                </div>
                            </div>

                            <div class="grid grid-cols-2 gap-3 mx-2 my-3">
                                <div class="">
                                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Producto</label>
                                    <span class="flex-1 mt-1 block w-full py-2 px-3 sm:text-sm ">{{ bk_item.item }}</span>                                
                                </div>
