<template >
    <div>
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores: <br><br><b v-html="info_message"></b></span>
        </div>
        <form action="" class="mt-2"
        v-if="showForm"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                        
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Seleccionar un Nombre de Clase existente</label>
                <input type="checkbox" name="isnew" :checked="is_new_cn" v-on:change="is_new_cn = !is_new_cn">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre de Clase</label>
                <model-select :options="cn_list"
                        v-model="class_name"
                        placeholder="Seleccione nombre de clase existente"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre de Clase</label>
                <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <br>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre de Clase</label>
                <model-select :options="countries"
                        v-model="formData.country"
                        placeholder="Seleccione nombre de clase existente"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select> 
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
            <button class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
            hover:bg-green-500 focus:outline-none 
            focus:border-green-700 focus:ring focus:ring-green-200
            active:bg-green-600 
            disabled:opacity-25 transition">Save</button>            
            <button class="inline-flex border border-gray-400 bg-red-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
            hover:text-white 
            focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
            active:text-gray-800 active:bg-red-50 
            disabled:opacity-25 transition">Cancel</button>            
        </div> 

        </form>
    </div>
</template>
<script>
import GalleryPhotoFilterService from "@/services/gallery/gallery_photo_filter.service"
import GalleryFilterService from "../../../services/gallery/gallery_filter.service";

import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css';

export default {
    name: "CrudGalleryPhotoFilter",
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    components: {
        ModelSelect
    },
    data() {
        return {
            showForm: true,
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            info_message: '',
            error_message_text: '',
            error_message: false,
            success_message: false,
            is_new_cn: false,
            filetrs: [],
            cn_list: [],
            class_name: 0
        }
    },
    created() {        
        GalleryFilterService.getSimpleList().then(c => {
            this.filetrs = c
            this.validateInitInfo()
        })
        GalleryFilterService.getClassNameList().then(c => this.cn_list)
        
        this.initForm()
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    methods: {
        validateInitInfo() {

            // Si combos vacios redirigir a tablas que hay que llenar 
            if(this.filters.length === 0) {
                this.showForm = false
                this.info_message += (this.countries.length == 0) ? ' Filtros <br>' : ''
            }
        },
        initForm() {
            GalleryPhotoFilterService.getGalleryPhotoFilter(this.info).then(e => {
                if(this.mode != 'create') {
                    this.ancho = e.width
                    this.alto = e.height
                }
            })
        },
        submitForm(e) {
            e.preventDefault();   

            if(this.alto > 0 && this.ancho > 0) {
                
                GalleryPhotoFilterService.addUpdGallery(formdata, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000);                        
                    }  
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Los valores del filtro deben ser mayores a 0'
                setTimeout(() => {
                    this.error_message = false
                }, 3000);    
            }
        }
    }
}
</script>
