<template >
    <div>          
        <form class="mt-2"
        @submit="submitForm">  
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                
                <div class="flex flex-row items-center my-3 mx-2">            
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Activo</label>
                    <input v-model="formData.active" type="checkbox" class="p-2" >
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Siglas</label>
                    <input v-model="formData.name" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Descripcin</label>
                    <input v-model="formData.description" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2" v-show="(formData.name != 'CONTRA')">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Tipo de producto</label>
                    <model-select :options="product_types"
                            v-model="formData.product_type_id"
                            placeholder="Seleccione un producto *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
            </div>            

            <!-- Button -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load">
                        Salvar&nbsp;
                        <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>          
                <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="flex bg-red-500 rounded-3xl w-full text-left text-4xl text-white p-2">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center text-white px-2">
                      <p>{{error_message_text}}</p>
                    </h5>
                </div>
                <div v-if="success_message" class="flex bg-green-500 rounded-3xl w-full text-left text-4xl text-white p-2">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center text-white px-2">{{success_message_text}}</h5>
                </div>                           
            </div>

        </form>
    </div>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import RateTypeService from "@/services/rate/rate_type.service";
import ProductTypeService from "@/services/common/product_type.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudRateType",
    components: {        
        ModelSelect
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,  
            product_types: [],
            formData: {},
        }
    },
    created() {
        // Init country select
        ProductTypeService.getSimpleList().then(p => {this.product_types = p})
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            RateTypeService.getRateType(this.info).then(e => {
                this.clearForm()
                if(this.mode != 'create') {
                    this.formData.name = e.name
                    this.formData.active = e.active
                    this.formData.description = e.description                    
                    this.formData.product_type_id = e.product_type.id
                    this.formData.id = e.id
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();                  
            if(this.validate(this.formData.name) ) {                             
                this.load = true
                const data = {                    
                    name: this.formData.name,
                    product_type_id: (typeof this.formData.product_type_id == 'object') ? this.formData.product_type_id.value : this.formData.product_type_id,
                    description: this.formData.description,
                    active: (this.formData.active) ? 1 : 0,
                }
                
                if('id' in this.formData) data.id = this.formData.id

                RateTypeService.addUpdRateType(data, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        async getRateType(e) {
            console.log(e)
        },
        clearForm() {
            this.formData = {
                active: '',
                name: '',
                description: '',
                product_type_id: {
                    value: 0,
                    text: '',
                }
            }
        },
    }
}
</script>
