<template >
    <div>          
        <form action="" class="mt-2"
        @submit="submitForm">                         
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Discount</label>
                    <input v-model="formData.discount" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Product Type</label>
                    <select class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        <option value="">Product Type1</option>
                        <option value="">Product Type2</option>
                        <option value="">Product Type3</option>
                    </select>                    
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Products</label>
                    <select class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        <option value="">Product1</option>
                        <option value="">Product2</option>
                        <option value="">Product3</option>
                    </select>                    
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">bookingw Ini</label>
                    <input v-model="formData.bookingw_ini" type="date" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">bookingw End</label>
                    <input v-model="formData.bookingw_end" type="date" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Travelw Ini</label>
                    <input v-model="formData.travelw_ini" type="date" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Travelw End</label>
                    <input v-model="formData.travelw_end" type="date" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                               
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Early Booking Days</label>
                    <input v-model="formData.early_booking_days" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Min Nights</label>
                    <input v-model="formData.min_nights" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>

            </div>            

            <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition">Save</button>            
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition">Cancel</button> 
                <div v-if="error_message" class="flex bg-red-500 rounded-3xl w-full text-left text-4xl p-4 my-3">

                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="flex bg-green-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div>

        </form>
    </div>
</template>
<script>

import { ModelSelect } from 'vue-search-select'
import ProductService from "@/services/rate/rate_offer.service";
import ProductTypeService from "@/services/common/product_type.service";
import RateOfferService from "@/services/rate/rate_offer.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudRateOffer",
    components: {        
        ModelSelect,
    },
    data() {
        return {
            error_message_text: '',
            error_message: false,
            success_message: false,

            product_types: [],
            products: [],
            formData: {},
        }
    },
    created() {
        // Init country select
        
        //ProductService.getSimpleList().then(c => this.products = c)
        //ProductTypeService.getSimpleList().then(c => this.product_types = c)
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action                
            if(this.mode != 'create') {
                this.formData.id = this.info.id
                this.formData.discount = this.info.discount
                this.formData.product_type_id = this.info.product_type_id
                this.formData.product_id = this.info.product_id
                this.formData.bookingw_ini = this.info.bookingw_ini
                this.formData.bookingw_end = this.info.bookingw_end
                this.formData.early_booking_days = this.info.early_booking_days
                this.formData.min_nights = this.info.min_nights
                this.formData.travelw_ini = this.info.travelw_ini
                this.formData.travelw_end = this.info.travelw_end                
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();            
            if(this.validate(this.formData.discount) || this.validate(this.formData.product_type_id) || this.validate(this.formData.product_id) ||
            this.validate(this.formData.bookingw_ini) || this.validate(this.formData.bookingw_end) || this.validate(this.formData.early_booking_days) ||
            this.validate(this.formData.min_nights) || this.validate(this.formData.travelw_ini) || this.validate(this.formData.travelw_end)) {   
                /* this.formData.parent_id = this.formData.parent
                this.formData.country_id = this.formData.country  */               
                
                RateOfferService.addUpdRateOffer(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = false
                this.success_message = true
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getRateOffer(e) {
            console.log(e)
        },
        clearForm() {
            this.formData = {
                id: '',
                discount: '',
                bookingw_ini: '',
                bookingw_end: '',
                early_booking_days: '',
                min_nights: '',
                travelw_ini: '',
                travelw_end: '',
                
                product_type_id: '',
                product_type: {
                    value: '',
                    text: ''
                },

                product_id: '',
                product: {
                    value: '',
                    text: ''
                },
            }
        },
    }
}
</script>
