import Authorization from "../user/auth.service";

const BookingStatusService = {
    getList() {
        try {
            return Authorization.checkLogin(`/booking-status`,{ auth: 'bearer' }).then(res => {
                                
                if (res.status !== 200){
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }

                const list = [];
                for (let item of res.data) {                    
                     
                    const option = {
                        id: item.id,
                        name: item.name,
                        description: item.description
                    };
                        
                    list.push(option);
                }
                
                return list;                
            })
            .catch (e => { console.error(e) }) 
        } catch (e) {
            console.error(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/booking-status`,{ auth: 'bearer' }).then(res => {
                                
                if (res.status !== 200){
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }

                const list = [];
                for (let item of res.data) {
                    if (item.id !== 1) {
                     
                        const option = {
                            value: item.id,
                            text: item.description
                        };
                        
                        list.push(option);
                    }
                }
                
                return list;                
            })
            .catch (e => { console.error(e) }) 
        } catch (e) {
            console.error(e)
        }
    },
    /* getSimpleList(){

    }, */
    async getBookingStatus(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/booking-status/${id}`,{ auth: 'bearer' })
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.error(e.response)
        }
    },
    async addUpdBookingStatus(rate_data, is_new = true) {
        try {
            //console.log(rate_data)
            rate_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/booking-status/create', rate_data, 'post'):
                await Authorization.checkLogin(`/booking-status/upd/${rate_data.id}`, rate_data, 'put')
            if (status === 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.error(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/rate-type/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.error(e.response)
        }
    }
}



export default BookingStatusService
