import Authorization from "../user/auth.service";

const RateOfferService = {
    getList() {
        try {
            return Authorization.checkLogin(`/rate-offer`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            product_type_id: item.product_type_id,
                            product_id: item.product_id,
                            bookingw_ini: item.bookingw_ini,
                            bookingw_end: item.bookingw_end,
                            min_nights: item.min_nights,
                            //discount: item.discount,
                            //early_booking_days: item.early_booking_days,
                            //travelw_ini: item.travelw_ini,
                            //travelw_end: item.travelw_end,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    async getRateOffer(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/rate-offer/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdRateOffer(rate_data, is_new = true) {
        try {
            //console.log(rate_data)
            rate_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/rate-offer/create', rate_data, 'post'):
                await Authorization.checkLogin(`/rate-offer/upd/${rate_data.id}`, rate_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/rate-offer/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default RateOfferService