import Authorization from "../user/auth.service";

const PoleTypeService = {
    getList() {
        try {
            return Authorization.checkLogin(`/poles-type`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    async getPoleType(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/poles-type/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdPoleType(pole_data, is_new = true) {
        try {
            pole_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/poles-type/create', pole_data, 'post'):
                await Authorization.checkLogin(`/poles-type/upd/${pole_data.id}`, pole_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/poles-type/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default PoleTypeService