                <model-select :options="rooms"
                    v-model="room"
                    placeholder="Seleccione Habitacion"
                    required
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                      
            </div>
            <div class="flex flex-row items-center w-70x100 my-3 mx-2">
