                <template #input="picker">
                    <span class="text-darkGray w-full text-center">
                        {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                    </span>
                </template>                
                </date-range-picker> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Tipo de tarifa</label>
                <model-select :options="tariff_types"
                    v-model="tariff"
                    placeholder="Seleccione el tipo de tarifa"
                    required
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                      
            </div>
            <!-- Data only for not contract tariff type -->
            <div class="flex flex-col items-start w-70x100 my-3" v-if="!isContract">
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Rango de fechas de reserva</label>
                    <date-range-picker                    
                        ref="picker"
                        :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                        v-model="bookingRange"
                        :time-picker="false"
                        :autoApply="true"
                        :ranges="false"
                        @update="getBookingDates"
                        class="dateRangePicker flex-1 border mt-1 block px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                    >
                    <template #input="picker">
                        <span class="text-darkGray w-full text-center">
                            {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                        </span>
                    </template>                
                    </date-range-picker> 
                </div>
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">das de reserva anticipada</label>
                    <input type="text" 
                    v-model="form.early_booking_days"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">min nights</label>
                    <input type="text"
                    v-model="form.min_nights"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                    
            </div>                
            <div class="flex flex-1 justify-end mt-6">
                <small class="text-red mr-auto px-5" :class="{'hidden': !emptytable}">
                   <i class="fas fa-exclamation-triangle"></i>
                    No aparecen precios para esa habitacin en el rango de fecha.
                </small>
                <button type="button" class="inline-flex gap-3 border border-transparent h-10 bg-blue rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-darkBlue focus:outline-none                     
                    active:bg-darkBlue
                    disabled:opacity-25 transition"
                    :disabled="spinner"
                    @click="getTableInfo">
                    <span v-show="spinner"> <i class="fa fa-spinner fa-pulse"></i></span>
                    Mostrar precios                    
                </button>            
            </div>            
        </div>   
         <!-- PRICES -->
        <div class="grid grid-cols-2 my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            <h2 class="inline text-darkGray text-xl ml-4 mb-3">Zona de precios</h2>
            <!-- <div class="flex flex-1 flex-row flex-wrap ml-32">
                <div class="flex flex-col items-end my-3 mx-2 flex-wrap">                               
                    <div class="flex flex-row"><label class="uppercase block text-sm font-medium text-gray-700 mx-2">second child free</label><input type="checkbox" class="p-2" ></div>
                    <div class="flex flex-row"><label class="uppercase block text-sm font-medium text-gray-700 mx-2">stop sale</label><input type="checkbox" class="p-2" ></div>
                </div>                                
                <div class="flex flex-col items-end my-3 mx-2 flex-wrap">            
                    <div class="flex flex-row"><label class="uppercase block text-sm font-medium text-gray-700 mx-2">first child one adult price free</label><input type="checkbox" class="p-2" ></div>
                    <div class="flex flex-row"><label class="uppercase block text-sm font-medium text-gray-700 mx-2">apply double price adult with one child</label><input type="checkbox" class="p-2" ></div>
                    
                </div>                
            </div> -->
            <div class="flex col-gap-4 col-span-2 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2">
                    aplicar precio por pax
                    <input type="checkbox" class="mx-2" v-model="form.apply_price_per_pax">
                </label>
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2">
                    aplicar precio doble a nios con adulto
                    <input type="checkbox" class="mx-2" v-model="form.apply_double_price_adult_with_one_child">
                </label>
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2">
                    detener ventas
                    <input type="checkbox" class="mx-2" v-model="form.stop_sale">
                </label>
            </div>
            
            <div class="grid grid-cols-3 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">simple</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.single_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white shadow-sm 
                    rounded-tl-md rounded-bl-md
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>
                </div>
            </div>
            <div class="grid grid-cols-3 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">doble <b class="text-red">*</b></label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.double_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>
                </div>
            </div>
            <div class="grid grid-cols-3 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">triple</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.triple_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>
                </div>
            </div>
            <div class="grid grid-cols-3 items-center my-3 mx-2">                
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">primer hijo</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.first_child_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>
                </div>
                <div class="flex flex-row items-center justify-center">
                    <input type="checkbox" class="p-2" v-model="form.first_child_free">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">gratis</label>
                </div>                
            </div>
            <div class="grid grid-cols-3 items-center my-3 mx-2">                
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">segundo hijo</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.second_child_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>    
                </div>                
                <div class="flex flex-row items-center justify-center">
                    <input type="checkbox" class="p-2" v-model="form.second_child_free">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">gratis</label>
                </div>                
            </div>  
            <div class="grid grid-cols-3 items-center my-3 mx-2">                
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Primer hijo con adulto</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.first_child_one_adult_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">$</span>
                </div>
                <div class="flex flex-row items-center justify-center">
                    <input type="checkbox" class="p-2" v-model="form.first_child_one_adult_price_free">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">gratis</label>
                </div>                
            </div>                      
            <div class="grid grid-cols-3 items-center my-3 mx-2">                
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Segundo Nio con Adulto</label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.second_child_one_adult_price"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                               
                    <span class="info-input">$</span>
                </div>  
                <div class="flex flex-row items-center justify-center">
                    <input type="checkbox" class="p-2" v-model="form.second_child_one_adult_price_free">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2">gratis</label>
                </div>              
            </div>                      
            <div class="grid grid-cols-3 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Marginacin <b class="text-red">*</b></label>
                <div class="flex items-center">
                    <input type="text" 
                    v-model="form.margin"
                    class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    <span class="info-input">%</span>
                </div>
            </div>            
            <!-- <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Cost</label>
                <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Sale</label>
                <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div> -->
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 gap-y-4">
            <div class="flex flex-col mr-auto">
                <small class="text-darkGray">Rellenar todos los campos marcados con <b class="text-red">*</b></small>
                <transition name="fade">
                    <small class="bg-red text-white px-3 py-2 mt-3" v-if="show_error"><i class="fa fa-info-circle"></i> {{ error_message }}</small>
                </transition>
            </div>                                   
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                hover:text-white 
                focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                active:text-gray-800 active:bg-red 
                disabled:opacity-25 transition"
                @click="clearAll()">Cancel
            </button>         
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                hsover:bg-green-500 focus:outline-none 
                focus:border-green-700 focus:ring focus:ring-green-200
                active:bg-green-600 
                disabled:opacity-25 transition">
                <span class="pr-2" v-if="submited">
                    <i class="fa fa-spinner fa-pulse"></i>
                </span>
                    Save
            </button>                              
        </div>

        <!-- Table -->          
        <div class="border-8 rounded-md my-10 p-4" v-if="showData">        
            <small class="text-darkGray" v-if="showData"><b>{{ search_info }}</b></small>
            <admin-table :deleteCols="deleteCols" :actions="actions" :rows="dataTable" v-on:deleteData="deletePrices"></admin-table>
        </div>
    </form>
</template>
<script>
import moment from 'moment'
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css'

import AdminTable from '@/components/Admin/AdminTable';

// Services
import HotelService from '@/services/hotel/hotel.service';
import RateTypeService from '@/services/rate/rate_type.service';
import HotelRoomPriceService from '@/services/hotel/hotel_room_price.service';

export default {
    name: "CrudHotelRoomPrice",
    components: {
        ModelSelect,
        DateRangePicker,
        AdminTable
    },
    filters: {
        dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
        }
    },
    computed: {        
        showData() {
            return (this.dataTable.length > 0)
        }
    },
    data() {
        return {
            emptytable: false,
            spinner: false,
            actions: {delete: true},
            deleteCols: ['tarifa_id'],
            isContract: true,
            submited: false,
            show_error: false,
            error_message: 'Faltan campos obligatorios por rellenar',
            search_info: '',
            dataTable: [],
            hotels: [],
            rooms: [],
            room: '',
            tariff_types: [],
            hotel: 0,
            tariff: null,
            dateRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            bookingRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            form: {}               
        }
    },
    created() {
        HotelService.getSimpleList().then(h => {this.hotels = h})
        //     this.hotels.push({value: 0, text: '...'})
        //     Array.prototype.push.apply(this.hotels,h)
        // })
        RateTypeService.getSimpleList(1).then(t => {this.tariff_types = t})
        //     this.tariff_types.push({value: 0, text: '...'})
        //     Array.prototype.push.apply(this.tariff_types,t)
        // })
        this.clearAll()
    },
    watch: {
        hotel(a){            
            let hotel = this.hotels.find(e => e.value == a)
            if(hotel) {
                this.search_info = hotel.text
                this.form.hotel_id = this.hotel                        
                
                HotelService.getRoomList(this.hotel).then(rooms => {
                    this.rooms = []
                    for(let r of rooms){
                        this.rooms.push({
                            text: r.alias,
                            value: r.id,
                        })
                    }
                })
            }
        },
        room(r) {
            this.form.room_id = r
            let room = this.rooms.find(e => e.value == r)
            this.search_info += ' - ' + room.text            
        },
        tariff() {
            this.form.tariff_id = (this.tariff == null || this.tariff == undefined) ? 0 : this.tariff
            this.isContract = (this.tariff <= 1 || this.tariff == null || this.tariff == undefined)                               
        },
    },
    methods: {
        async getTableInfo() {
            // Search info from table             
            try {
                this.spinner = true                
                this.form.tariff_id = (this.form.tariff_id) || 0
                HotelRoomPriceService.searchRoomPrices(this.form).then(prices => {                                        
                    if(prices.length > 0) {
                        this.dataTable = (prices) ? prices : []
                    } else {
                        this.emptytable = true                        
                        setTimeout(() => { this.emptytable = false}, 3000)
                    }
                    this.spinner = false
                })
            } catch(error) {
                console.log(error)
            }
        },
        getRangeDates(objDate) {            
            this.form.date_from = moment(objDate.startDate).format('yyyy-MM-DD')
            this.form.date_to = moment(objDate.endDate).format('yyyy-MM-DD')
        },
        getBookingDates(objDate) {            
            this.form.date_booking_ini = moment(objDate.startDate).format('yyyy-MM-DD')
            this.form.date_booking_end = moment(objDate.endDate).format('yyyy-MM-DD')
        },
        setPrices(e) {
            e.preventDefault(); 
            this.submited = true
            if(this.validateParams()){
                try {
                    HotelRoomPriceService.addPrices(this.form).then(res => {
                        this.dataTable = res
                        setTimeout(() => {
                            this.submited = false                    
                        }, 3000)
                        this.clearAll()
                    })
                } catch(error) {
                    console.log(error)
                }                
            }                
        },
        deletePrices(ids) {            
            try {
                // get all objects to delete
                const tariff_ids = []
                ids.map(e => {                    
                    let obj = this.dataTable.find(d => d.id == e)
                    tariff_ids.push(obj.tarifa_id)
                })
                HotelRoomPriceService.deletePrices({ids,tariff_ids}).then(res => {
                    this.dataTable = res
                    setTimeout(() => {
                        this.submited = false                    
                    }, 3000)
                    this.clearAll()
                })
            } catch(error) {
                console.log(error)
            } 
        },
        clearAll() {            
            this.hotel = 0
            this.form = {
                hotel_id: 0,
                room_id: 0,
                tariff_id: null,    
                date_from: moment().add(3, 'days').format('yyyy-MM-DD'),
                date_to: moment().add(4, 'days').format('yyyy-MM-DD'),
                stop_sale: 0,
                // Special Tariff
                date_booking_ini: '',
                date_booking_end: '',
                early_booking_days: 0,
                min_nights: 1,
                // Prices
                apply_price_per_pax: 0,
                apply_double_price_adult_with_one_child: 0,                
                single_price: 0,
                double_price: 0,
                triple_price: 0,
                first_child_price: 0,
                first_child_free: 0,
                first_child_one_adult_price: 0,
                first_child_one_adult_price_free: 0,                
                second_child_one_adult_price_free: 0,                
                second_child_price: 0,
                second_child_free: 0,
                second_child_one_adult_price: 0,
                margin: 18,
            }
            this.rooms = []
            this.tariff = null
            this.isContract = true
            this.dateRange = {
                startDate: moment().add(3, 'days').format('yyyy-MM-DD'),
                endDate: moment().add(4, 'days').format('yyyy-MM-DD'),
            }
            this.bookingRange = {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            }
        },
        validateParams(){               
            // console.log(this.form)
            if(this.form.hotel_id == 0 || this.form.room_id == 0 || this.form.date_from == '' || this.form.date_to == '' 
            || this.form.double_price == 0 || this.form.margin == 0){
                this.show_error = true
                this.submited = false
                setTimeout(() => {
                    this.show_error = false                    
                }, 3000)
                return false
            }
            return true
        }
    }
}
</script>
<style >
    .dateRangePicker.searcher .form-control{
        /* font-size: 1.25rem; */    
        text-align: left;
        background: transparent !important;
        border: none !important;    
        width: 30%;
    }

    .info-input {
        width: 30px;
        background: #c4c4c4;
        color: var(--dark-gray);
        padding: 7px 10px;
        border-bottom: 1px solid #c4c4c4;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
    }
</style>