import Authorization from "../user/auth.service";

const HotelRoomTypeCategoryService = {
    getList() {
        try {
            return Authorization.checkLogin(`/hotel-room-type-category`).then(res => {
                const list = []                
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            name: item.name,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.name
                })
            }
            return result
        })

    },
    async getHotelRoomTypeCategory(id) { //! Obtiene los datos de un Hotel dado un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/hotel-room-type-category/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdHotelRoomTypeCategory(hotel_data, is_new = true) {
        try {
            hotel_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/hotel-room-type-category/create', hotel_data, 'post'):
                await Authorization.checkLogin(`/hotel-room-type-category/upd/${hotel_data.id}`, hotel_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/hotel-room-type-category/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default HotelRoomTypeCategoryService