// src/services/encryption.service.js
export const EncryptionService = {
  /**
   * Cifrar texto usando RSA-OAEP con SHA-256 (IDNTICO al HTML)
   * @param {string} text - Texto a cifrar
   * @param {string} publicKeyPem - Clave pblica en formato PEM
   * @returns {string} - Texto cifrado en Base64
   */
  async encryptForBackend(text, publicKeyPem) {
    try {
      if (!text || !publicKeyPem) {
        throw new Error('Texto y clave pblica son requeridos');
      }

      //  IDNTICO AL HTML - NO NORMALIZAR, tratar \n como literales
      const pemToArrayBuffer = (pem) => {
        
        //  EXACTAMENTE IGUAL AL HTML
        const b64 = pem
          .replace(/-----BEGIN PUBLIC KEY-----/g, "")
          .replace(/-----END PUBLIC KEY-----/g, "")
          .replace(/\\n/g, "")      //  elimina literales "\n" - IGUAL AL HTML
          .replace(/\s+/g, "")      // elimina espacios o saltos reales - IGUAL AL HTML
          .trim();
        
        // Verificar que el base64 sea vlido
        if (!b64 || b64.length < 100) {
          throw new Error(`Base64 invlido o muy corto. Longitud: ${b64.length}`);
        }
        
        try {
          const binaryString = atob(b64);
          const len = binaryString.length;
          const bytes = new Uint8Array(len);
          for (let i = 0; i < len; i++) {
            bytes[i] = binaryString.charCodeAt(i);
          }
          return bytes.buffer;
        } catch (e) {
          throw new Error('Error decodificando Base64: ' + e.message);
        }
      };

      //  IDNTICO AL HTML
      const arrayBufferToBase64 = (buffer) => {
        const bytes = new Uint8Array(buffer);
        let binary = '';
        for (let i = 0; i < bytes.byteLength; i++) {
          binary += String.fromCharCode(bytes[i]);
        }
        return btoa(binary);
      };

      // 1. Convertir PEM a ArrayBuffer (IGUAL AL HTML)
      const spki = pemToArrayBuffer(publicKeyPem); //  NO normalizar antes
      
      if (spki.byteLength === 0) {
        throw new Error('ArrayBuffer de la clave pblica est vaco');
      }
      

      // 2. Importar la clave pblica (IGUAL AL HTML)
      const publicKey = await window.crypto.subtle.importKey(
        'spki',
        spki,
        {
          name: 'RSA-OAEP',
          hash: { name: 'SHA-256' } // SHA-256 explcito - IGUAL AL HTML
        },
        false, // no es extractable - IGUAL AL HTML
        ['encrypt'] // usos permitidos - IGUAL AL HTML
      );
      

      // 3. Codificar el texto plano (IGUAL AL HTML)
      const encoded = new TextEncoder().encode(text);

      // 4. Cifrar (IGUAL AL HTML)
      const cipherBuffer = await window.crypto.subtle.encrypt(
        {
          name: 'RSA-OAEP' // IGUAL AL HTML
        },
        publicKey,
        encoded
      );

      // 5. Convertir a Base64 (IGUAL AL HTML)
      const cipherBase64 = arrayBufferToBase64(cipherBuffer);
      
      return cipherBase64;

    } catch (error) {
      console.error(' Error en encryptForBackend:', error);
      throw new Error(`Fallo en cifrado RSA: ${error.message}`);
    }
  },

  /**
   * Validar la clave pblica
   */
  validatePublicKey(publicKey) {
    if (!publicKey) {
      throw new Error('La clave pblica es requerida');
    }
    
    if (publicKey.length < 50) {
      throw new Error('La clave pblica parece ser invlida (demasiado corta)');
    }
    
    return publicKey;
  },

  /**
   * Probar el cifrado/descifrado (para debugging)
   */
  async testEncryption(publicKeyPem, testText = 'test123') {
    try {
      
      const encrypted = await this.encryptForBackend(testText, publicKeyPem);      

      return {
        success: true,
        original: testText,
        encrypted: encrypted,
        encryptedLength: encrypted.length
      };
    } catch (error) {
      console.error(' Test fallido:', error);
      return {
        success: false,
        error: error.message
      };
    }
  }
};

export default EncryptionService;