<template >
    <form action="" class="mt-2"
    @submit="setPrices">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            <h2 class="inline text-darkGray text-xl ml-4 mb-3">Zona de busqueda</h2>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Cargar precio por</label>
                <label class="block text-sm font-medium text-gray-700 mx-2">
                    <input type="radio" class="py-2 px-3 border-gray-400 rounded-md shadow-sm" value="1" v-model="price_type">
                    Categoria y tipo de mecnica
                </label>
                <label class="block text-sm font-medium text-gray-700 mx-2">
                    <input type="radio" name class="py-2 px-3 border-gray-400 rounded-md shadow-sm" value="2" v-model="price_type">
                    Carro
                </label>
            </div>
            <div class="flex flex-row items-center my-3 mx-2" v-show="(price_type == 2)">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Autos <b class="text-red">*</b></label>
                <model-select :options="cars"
                    v-model="formData.car_id"
                    placeholder="Seleccione Auto"
                    required                    
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                </model-select>                
            </div>     
            <div class="flex flex-row items-center my-3 mx-2" v-show="(price_type == 1)">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Categora <b class="text-red">*</b></label>
                <model-select :options="categories"
                    v-model="formData.category_id"
                    placeholder="Seleccione Auto"
                    required                    
                    class="flex-1 border mt-1 py-2 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                
            </div>
            <div class="flex flex-row items-center my-3 mx-2" v-show="(price_type == 1)">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Mecnica <b class="text-red">*</b></label>
                <model-select :options="engines"
                    v-model="formData.engine_id"
                    placeholder="Seleccione Auto"
                    required                    
                    class="flex-1 border mt-1 py-2 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>
            </div>                     
            <div class="flex flex-row items-center w-70x100 my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Rango de fechas</label>
                <date-range-picker                    
                    ref="picker"
                    :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                    v-model="dateRange"
                    :time-picker="false"
                    :autoApply="true"
                    :ranges="false"
                    @update="getRangeDates"
                    class="dateRangePicker flex-1 border mt-1 block w-30 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                >
                <template #input="picker">
                    <span class="text-darkGray w-full text-center">
                        {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                    </span>
                </template>                
                </date-range-picker> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Tipo de tarifa</label>
                <model-select :options="tariff_types"
                    v-model="formData.tariff_id"
                    placeholder="Seleccione el tipo de tarifa"
                    required
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                      
            </div>
            <!-- Data only for not contract tariff type -->
            <div class="flex flex-col items-start w-70x100 my-3" v-if="!isContract">
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Rango de fechas de reserva</label>
                    <date-range-picker                    
                        ref="picker"
                        :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                        v-model="bookingRange"
                        :time-picker="false"
                        :autoApply="true"
                        :ranges="false"
                        @update="getBookingDates"
                        class="dateRangePicker flex-1 border mt-1 block px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                    >
                    <template #input="picker">
                        <span class="text-darkGray w-full text-center">
                            {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                        </span>
                    </template>                
                    </date-range-picker> 
                </div>
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">das de reserva anticipada</label>
                    <input type="text" 
                    v-model="formData.early_booking_days"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center w-full my-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">min nights</label>
                    <input type="text"
                    v-model="formData.min_nights"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                    
            </div>                
            <div class="flex flex-1 justify-end mt-6">
                <small class="text-red mr-auto px-5" :class="{'hidden': !emptytable}">
                   <i class="fas fa-exclamation-triangle"></i>
                    No aparecen precios para ese auto en el rango de fecha.
                </small>
                <button type="button" class="inline-flex gap-3 border border-transparent h-10 bg-blue rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-darkBlue focus:outline-none                     
                    active:bg-darkBlue
                    disabled:opacity-25 transition"
                    :disabled="spinner"
                    @click="getTableInfo">
                    <span v-show="spinner"> <i class="fa fa-spinner fa-pulse"></i></span>
                    Mostrar precios                    
                </button>            
            </div>            
        </div>   
         <!-- PRICES -->
        <div class="flex flex-col gap-3 my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            <h2 class="inline text-darkGray text-xl ml-4 mb-3">Zona de precios</h2>                        
            <div class="flex gap-3">
                <div class="flex flex-col gap-3 my-3 mx-2 w-50">
                    <div class="flex gap-3">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-30">Precio de 3 a 6 das <b class="text-red">*</b></label>
                        <div class="flex items-center">
                            <input type="text" 
                            v-model="formData.price_3a6"
                            class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white shadow-sm 
                            rounded-tl-md rounded-bl-md
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            <span class="info-input">$</span>
                        </div>
                    </div>
                    <div class="flex gap-3">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-30">Precio de 7 a 30 das <b class="text-red">*</b></label>
                        <div class="flex items-center">
                            <input type="text" 
                            v-model="formData.price_7a30"
                            class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white shadow-sm 
                            rounded-tl-md rounded-bl-md
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            <span class="info-input">$</span>
                        </div>
                    </div>
                    <div class="flex gap-3">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-30">Precio ms de 30 das <b class="text-red">*</b></label>
                        <div class="flex items-center">
                            <input type="text" 
                            v-model="formData.price_mas30"
                            class="flex-1 border block w-full py-2 px-3 border-gray-400 bg-white shadow-sm 
                            rounded-tl-md rounded-bl-md
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            <span class="info-input">$</span>
                        </div>
                    </div>
                </div>
                <div class="flex flex-col gap-3 my-3 mx-2 w-50">
                    <div class="flex col-gap-4 col-span-2 items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2">
                            detener ventas
                            <input type="checkbox" class="mx-2" v-model="formData.stop_sale">
                        </label>
                    </div>                    
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Marginacin <b class="text-red">*</b></label>
                    <div class="flex items-center">
                        <input type="text" 
                        v-model="formData.margin"
                        class="border py-2 px-3 border-gray-400 bg-white rounded-tl-md rounded-bl-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        <span class="info-input">%</span>
                    </div>
                </div>                        
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 gap-y-4">
            <div class="flex flex-col mr-auto">
                <small class="text-darkGray">Rellenar todos los campos marcados con <b class="text-red">*</b></small>
                <transition name="fade">
                    <small class="bg-red text-white px-3 py-2 mt-3" v-if="show_error"><i class="fa fa-info-circle"></i> {{ error_message }}</small>
                </transition>
            </div>                                   
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                hover:text-white 
                focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                active:text-gray-800 active:bg-red 
                disabled:opacity-25 transition"
                @click="clearAll()">Cancel
            </button>         
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                hsover:bg-green-500 focus:outline-none 
                focus:border-green-700 focus:ring focus:ring-green-200
                active:bg-green-600 
                disabled:opacity-25 transition">
                <span class="pr-2" v-if="submited">
                    <i class="fa fa-spinner fa-pulse"></i>
                </span>
                    Save
            </button>                              
        </div>

        <!-- Table -->          
        <div class="border-8 rounded-md my-10 p-4" v-if="showData">        
            <small class="text-darkGray" v-if="showData"><b>{{ search_info }}</b></small>
            <admin-table :deleteCols="deleteCols" :actions="actions" :rows="dataTable" v-on:deleteData="deletePrices"></admin-table>
        </div>
    </form>
</template>
<script>
import moment from 'moment'
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css'

import AdminTable from '@/components/Admin/AdminTable';

// Services
import CarService from '@/services/car/car.service';
import CarCategoryServices from "@/services/car/car_category.service";
import CarEngineServices from "@/services/car/car_engine.service";
import CarPriceService from '@/services/car/car_price.service';
import RateTypeService from '@/services/rate/rate_type.service';

export default {
    name: "CrudCarPrice",
    components: {
        ModelSelect,
        DateRangePicker,
        AdminTable
    },
    filters: {
        dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
        }
    },
    computed: {        
        showData() {
            return (this.dataTable.length > 0)
        }
    },
    data() {
        return {
            emptytable: false,
            spinner: false,
            actions: {delete: true},
            deleteCols: ['tarifa_id'],
            isContract: true,
            submited: false,
            show_error: false,
            error_message: 'Faltan campos obligatorios por rellenar',
            search_info: '',
            price_type: 1,
            dataTable: [],
            cars: [],  
            categories: [],  
            engines: [],  
            tariff_types: [],
            dateRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            bookingRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            formData: {}               
        }
    },
    created() { 
        let data =[]
        data.push(CarCategoryServices.getSimpleList())
        data.push(CarEngineServices.getSimpleList())
        data.push(CarService.getSimpleList())
        data.push(RateTypeService.getSimpleList())
        
        Promise.all(data).then(d => {
            this.categories = d[0]
            this.engines = d[1]
            this.cars = d[2]
            this.tariff_types = d[3]
        }) 
        this.clearAll()
    },
    watch: {        
        tariff() {
            this.formData.tariff_id = this.tariff
            this.isContract = (this.tariff <= 1)
        },        
    },
    methods: {
        async getTableInfo() {
            // Search info from table   
            try {
                this.spinner = true
                CarPriceService.searchCarPrices(this.getFormInfo()).then(prices => {
                    if(prices.length > 0) {                        
                        this.dataTable = (prices) ? prices : []
                    } else {
                        this.emptytable = true
                        setTimeout(() => { this.emptytable = false}, 3000)
                    }
                    this.spinner = false
                })
            } catch(error) {
                console.log(error)
            }
        },
        getRangeDates(objDate) {            
            this.formData.date_from = moment(objDate.startDate).format('yyyy-MM-DD')
            this.formData.date_to = moment(objDate.endDate).format('yyyy-MM-DD')
        },
        getBookingDates(objDate) {            
            this.formData.date_booking_ini = moment(objDate.startDate).format('yyyy-MM-DD')
            this.formData.date_booking_end = moment(objDate.endDate).format('yyyy-MM-DD')
        },
        setPrices(e) {
            e.preventDefault();
            this.submited = true            
            if(this.validateParams()){
                // Agregando los precios y el margen
                const data = {
                    margin: this.formData.margin,
                    stop_sale: this.formData.stop_sale,
                    price_3a6: this.formData.price_3a6,
                    price_7a30: this.formData.price_7a30,
                    price_mas30: this.formData.price_mas30,                    
                    ...this.getFormInfo()
                }

                try {
                    CarPriceService.addPrices(data).then(res => {
                        this.dataTable = res
                        setTimeout(() => {
                            this.submited = false
                        }, 3000)
                        this.clearAll()
                    })
                } catch(error) {
                    console.log(error)
                }                
            }                
        },
        deletePrices(ids) {            
            try {
                // get all objects to delete
                const tariff_ids = []
                ids.map(e => {                    
                    let obj = this.dataTable.find(d => d.id == e)
                    tariff_ids.push(obj.tarifa_id)
                })
                const data = {
                    ids,
                    tariff_ids,
                    ...this.getFormInfo()
                }
                
                CarPriceService.deletePrices(data).then(res => {
                    this.dataTable = res
                    setTimeout(() => {
                        this.submited = false                    
                    }, 3000)                    
                })
            } catch(error) {
                console.log(error)
            } 
        },
        clearAll() {
            this.formData = {
                car_id: {
                    value: 0,
                    text: ''
                },  
                category_id: {
                    value: 0,
                    text: ''
                }, 
                engine_id: {
                    value: 0,
                    text: ''
                }, 
                tariff_id: {
                    value: 0,
                    text: 'CONTRA'
                },    
                date_from: moment().add(3, 'days').format('yyyy-MM-DD'),
                date_to: moment().add(4, 'days').format('yyyy-MM-DD'),
                stop_sale: 0,
                // Special Tariff
                date_booking_ini: '',
                date_booking_end: '',
                early_booking_days: 0,
                min_nights: 1,
                // Prices
                price_3a6: 0,
                price_7a30: 0,
                price_mas30: 0,
                margin: 12,
            }            
            this.isContract = true
            this.dateRange = {
                startDate: moment().add(3, 'days').format('yyyy-MM-DD'),
                endDate: moment().add(4, 'days').format('yyyy-MM-DD'),
            }
            this.bookingRange = {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            }
        },
        validateParams(){               
            // console.log(this.form)
            if(this.formData.car_id == 0 || this.formData.tariff_id == 0 
            || this.formData.date_from == '' || this.formData.date_to == '' 
            || this.formData.price == 0 || this.formData.margin == 0){
                this.show_error = true
                this.submited = false
                setTimeout(() => {
                    this.show_error = false                    
                }, 3000)
                return false
            }
            return true
        },
        getFormInfo() {
            const data = {                                    
                tariff_id: (typeof this.formData.tariff_id == 'object') ? this.formData.tariff_id.value : this.formData.tariff_id,
                date_from: this.formData.date_from,
                date_to: this.formData.date_to                    
            }
            // Tipo de precio
            if(this.price_type == 1) { // Precio por categoria y mecanica                
                data.category_id = (typeof this.formData.category_id == 'object') ? this.formData.category_id.value : this.formData.category_id
                data.engine_id = (typeof this.formData.engine_id == 'object') ? this.formData.engine_id.value : this.formData.engine_id
            } else 
                data.car_id = (typeof this.formData.car_id == 'object') ? this.formData.car_id.value : this.formData.car_id
                
            if(data.tariff_id != 1) {
                data.date_booking_ini = this.formData.date_booking_ini
                data.date_booking_end = this.formData.date_booking_end
                data.early_booking_days = this.formData.early_booking_days
                data.min_nights = this.formData.min_nights
            }
            
            return data
        }
    }
}
</script>
<style >
    .dateRangePicker.searcher .form-control{
        /* font-size: 1.25rem; */    
        text-align: left;
        background: transparent !important;
        border: none !important;    
        width: 30%;
    }

    .info-input {
        width: 30px;
        background: #c4c4c4;
        color: var(--dark-gray);
        padding: 7px 10px;
        border-bottom: 1px solid #c4c4c4;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
    }
</style>