import Authorization from "../user/auth.service";

const GalleryPhotoFilterService = {
    getList() {
        try {
            return Authorization.checkLogin(`/gallery-photo-filters`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            filter_id: item.filter_id,
                            class_name: item.class_name,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    /* getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.filter_keyword
                })
            }
            return result
        })

    }, */
    async getGalleryPhotoFilter(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/gallery-photo-filters/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async getPhotoFilterByClassName(cn) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/gallery-photo-filters/class-name/${cn}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async getClassNameList() {
        try {
            const { status, data } = await Authorization.checkLogin(`/gallery-photo-filters/class-name`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdPhotoFilter(gallery_data, is_new = true) {
        try {
            gallery_data.auth = 'bearer'
            console.log(gallery_data)
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/gallery-photo-filters/create', gallery_data, 'post'):
                await Authorization.checkLogin(`/gallery-photo-filters/upd/${gallery_data.id}`, gallery_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
}

export default GalleryPhotoFilterService