import Authorization from "../user/auth.service";

const base_url = "marketplace-orders";
const MarketplaceOrderService = {
  getList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {
            const option = {
              id: item.id,
              reference: item.reference,
              status: this.getStatusText(item.status),
              cliente: item?.marketplace_user?.email ? item.marketplace_user.email : '',
              order_date: new Date(item.order_date).toLocaleDateString('es-ES', { day: '2-digit', month: '2-digit', year: '2-digit' }),
            };

            list.push(option);
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },

  async getMarketplaceOrder(id) {
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`, { auth: "bearer" });
      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
      throw e; // Propagar el error para manejarlo en el componente
    }
  },
  
  async updateMarketplaceOrder(form_data) {
    try {
      if(!form_data) throw new Error('No form data');

      // Validar que el estado sea vlido
      if (!this.isValidStatus(form_data.status)) {
        throw new Error('Estado invlido');
      }

      if(form_data.status === 'cancelled') {
        return await this.cancelOrder(form_data.id);
      } else {
        const payload = {
          auth: "bearer",
          status: form_data.status
        };
        
        const { status, data } = await Authorization.checkLogin(
          `/${base_url}/update-status/${form_data.id}`, 
          payload, 
          "put"
        );
        
        if (status === 200) {
          return data;
        } else {
          throw new Error('Error al actualizar el estado');
        }
      }
    } catch (e) {
      console.error(e.response);
      throw e; // Propagar el error
    }
  },

  async cancelOrder(id) {
    try {
      const { status, data } = await Authorization.checkLogin(
        `/${base_url}/cancel/${id}`, 
        { auth: "bearer" }, 
        "get"
      );
      
      if (status === 200) {
        return data;
      } else {
        throw new Error('Error al cancelar el pedido');
      }
    } catch (e) {
      console.error(e.response);
      throw e;
    }
  },

  // Validar que el estado sea uno de los permitidos
  isValidStatus(status) {
    return this.status_list.some(item => item.value === status);
  },

  getStatusText(statusValue) {    
    const status = this.status_list.find(item => item.value === statusValue);
    return status ? status.text : 'Desconocido';
  },

  status_list: [
    { value: 'created', text: "Pedido solicitado" },
    { value: 'sended', text: "Enviado" },
    { value: 'paid', text: "Pagado" },
    { value: 'completed', text: "Completado" },
    { value: 'cancelled', text: "Cancelado" },
  ],

  // Obtener estados disponibles basados en el estado actual
  getAvailableStatuses(currentStatus) {
    const statusFlow = {
      'created': ['sended', 'paid', 'cancelled'],
      'sended': ['completed', 'cancelled'],
      'paid': ['completed', 'cancelled'],
      'completed': [], // No se puede cambiar desde completado
      'cancelled': [] // No se puede cambiar desde cancelado
    };
    
    return this.status_list.filter(status => 
      statusFlow[currentStatus]?.includes(status.value)
    );
  }

};

export default MarketplaceOrderService;