<template>
    <div>          
        <form action="" class="mt-2" @submit="submitForm">
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">

                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Provincia</label>
                    <model-select 
                        :options="provinces"      
                        v-model="formData.marketplace_province_id"              
                        placeholder="Seleccione la provincia*"
                        required                           
                        class="border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>
                </div>

                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Municipio</label>
                    <input v-model="formData.name" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
            </div>            

            <!-- Button -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div>

        </form>
    </div>
</template>

<script>

import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import MarketplaceProvinceService from "@/services/marketplace/marketplace_province.service";
import MarketplaceCityService from "@/services/marketplace/marketplace_city.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudMarketplaceCity",
    components: {   
        ModelSelect,     
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,
            provinces: [],
            formData: {},  
        }
    },
    created() {
        // Init country select
        MarketplaceProvinceService.getSimpleList().then(el => this.provinces = el )
        this.initForm() 
    },
    // watch: {
    //     mode() {            
    //         this.initForm()
    //     },                     
    // },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            if(this.mode != 'create') {
                MarketplaceCityService.getMarketplaceCity(this.info).then(e => {
                    this.formData.id = e.id
                    this.formData.name = e.name
                    this.formData.marketplace_province_id = e.marketplace_province_id
                }) 
            }
            
        },
        clearForm() {
            this.formData = {
                name: '',
                marketplace_province_id: {
                    text: '', 
                    value: 0
                }
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();
            this.error_message = false
            this.success_message = true
            // console.log(this.formData)
            
            if(!this.validate(this.formData.name)){
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            }            
            
            if(!this.error_message)
            {       
                this.load = true            
                MarketplaceCityService.addUpdMarketplaceCity(this.formData, (this.mode === 'create'))
                .then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            }
        },
        async getMarketplaceCity(e) {
            console.log(e)
        }        
    }
}
</script>
