import Authorization from "../user/auth.service";

const base_url = 'car'
const CarService = {
    getList() {
        try {
            return Authorization.checkLogin(`/${base_url}-active`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            keyword: item.keyword,
                            category: item.car_category.alias,
                            engine: item.car_engine.alias,
                            paxes: item.num_pax,
                            electrico: item.is_electric,
                            'precio desde': item.price_from,
                            active: (item.active) ? true : false,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/${base_url}-active`).then(res => {
                if (res.status == 200) {
                    const countries = []
                    for (let item of res.data) {
                        countries.push({
                            value: item.id,
                            text: item.alias
                        })
                    }
                    return countries
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    async getCar(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {            
            console.log(e.response)            
        }
    },
    async addUpdCar(car_data, is_new = true) {
        try {
            car_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLoginImage(`/${base_url}/create`, car_data.form, 'post')
            : await Authorization.checkLoginImage(`/${base_url}/upd/${car_data.id}`, car_data.form, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default CarService