import Authorization from "../user/auth.service";

const GalleryService = {
    getList() {
        try {
            return Authorization.checkLogin(`/images`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            'Nombre Clase': item.class_name,
                            'Id objeto': item.id_object_class,
                            subcategoria: item.subcategory,
                            // lang: item.lang,
                            imagen: item.class_name+'_'+item.id,
                            original: item.image_keyword,
                            ext: item.ext,
                            ao: item.anno,
                            // order: item.order, 
                            // active: (item.active) ? true : false,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    /* getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.alias
                })
            }
            return result
        })

    }, */
    async getImage(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/image/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async getGallery(id,class_name) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/image/${class_name}/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdGallery(gallery_data, is_new = true) {
        try {
            gallery_data.auth = 'bearer'            
            const url = (is_new) ? '/images/create' : `/images/upd/${gallery_data.id}`

            const { status, data } = await Authorization.checkLoginImage(url, gallery_data.form, 'post')

            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/images/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default GalleryService