import Authorization from "../user/auth.service";

const HotelService = {
    getList() {
        try {
            return Authorization.checkLogin(`/hotels`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            offer: (item.in_offer) ? true : false,
                            active: (item.active) ? true : false,
                        })                        
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []                        
            for (let l of list) {                
                result.push({
                    value: l.id,
                    text: l.alias
                })                
            }            
            return result
        })

    },
    async getHotel(id) { //! Obtiene los datos de un Hotel dado un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/hotels/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdHotel(hotel_data, is_new = true, hasImage = true) {
        try {
            const url = (is_new) ? '/hotels/create' : `/hotels/upd/${hotel_data.id}`,
                method = (is_new) ? 'post' : 'put'

            hotel_data.auth = 'bearer'

            const {status, data} = (hasImage) ? 
                await Authorization.checkLoginImage(url, hotel_data.form, method) : 
                await Authorization.checkLogin(url, hotel_data, method)
            
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e)
        }
        return false
    },
    async getRoomList(id) {
        try {
            const { status, data } = await Authorization.checkLogin(`/hotel-rooms/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/hotels/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }

}

export default HotelService