import Authorization from "../user/auth.service";

const BookingService = {
    async getList() {
        try {
            const response = await Authorization.checkLogin(`/bookings-list`,{ auth: 'bearer' });
                        
            if (response.status === 200) {
                const list = response.data.map(item => ({
                    id: item.id,
                    booking_number: item.booking_number,
                    creation_date: item.creation_date,
                    status: `<span class="p-2 rounded" style="background: ${item.status.background_color}; color:${item.status.color};">${item.status.description}</span>`,
                    type: item.bk_items[0].type,
                    // email: item.pax[0].email,
                    // description: item.pax[0].description,
                    import: item.import,
                }));
                
                return list;
            } 
            else {
                throw new Error('Fail check login');
            }
        } catch (e) {
            console.error(e);            
            throw e;
        }
    },
    
    /* getSimpleList(){

    }, */
    async getBooking(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/bookings/${id}`,{ auth: 'bearer' })
            if (status === 200) {                
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdBooking(formData, is_new = false) {
        try {
            formData.auth = 'bearer';        

            const { status, data } = (is_new) 
                ? await Authorization.checkLogin('/booking/create', formData, 'post') 
                : await Authorization.checkLogin(`/booking/upd/${formData.id}`, formData, 'put')
            if (status === 200)
                return data;
            
        } catch (e) {
          //! Fail 
          console.log(e)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/booking/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}



export default BookingService
