<template >
    <div>   
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores: <br><br><b v-html="info_message"></b></span>
        </div>  
        <form action="" v-if="showForm" class="mt-2"
        @submit="submitForm">                         
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombres *</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.first_name" required>
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Apellidos *</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.last_name" required>
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Direccin</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.address">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Genero *</label>
                    <model-select :options="genders"
                            v-model="formData.gender"
                            placeholder="Seleccione genero *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>                   
                </div>
                <div class="flex flex-row items-center my-3 mx-2" v-if="is_admin">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Roles *</label>
                    <multi-select :options="roles"
                        :selected-options="formData.roles"
                        placeholder="Seleccione los roles"       
                        @select="onRoleSelect"                                 
                        required   
                        v-show="!is_vendor"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </multi-select>   
                    <input v-show="is_vendor" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-lightGray rounded-md shadow-sm sm:text-sm "
                     value="ROLE_Vendor" disabled>    
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Parent</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.parent" :disabled="!is_admin">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">EMail *</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.email" required>
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Telfono</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.phone">
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Ciudad</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.city">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Cdigo Postal</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.zip_code">
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Pas *</label>
                    <model-select :options="countries"
                            v-model="formData.country"
                            placeholder="Seleccione Pas *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>                   
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Provincia</label>
                    <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                    v-model="formData.province">
                </div>
                <!-- AGENCY OPTIONS -->
                <div class="flex flex-col border border-gray-400 rounded-md p-5 mt-3">
                    <div class="flex flex-row gap-x-8 items-center my-3 mx-2">            
                        <label class="uppercase block text-sm font-medium text-gray-700 ">Agencia de viajes</label>
                        <input type="checkbox" class="p-2" v-model="formData.is_agent" v-show="is_admin" :disabled="(is_agency || is_vendor)">
                        <label class="uppercase block text-sm font-medium text-gray-700 ml-2" v-show="is_admin">activo</label>
                        <input type="checkbox" class="p-2" v-model="formData.active" v-show="(is_admin || is_vendor)">                     
                        <label class="uppercase block text-sm font-medium text-gray-700 " v-show="(is_admin || is_vendor)">verificado</label>
                        <input type="checkbox" class="p-2" v-model="formData.verify" v-show="(is_admin || is_vendor)">
                    </div>                
                    
                    <div class="flex flex-row gap-x-5 items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 ">I A T A</label>
                        <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        v-model="formData.iata">
                        <label class="uppercase block text-sm font-medium text-gray-700 ">Compaa</label>
                        <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        v-model="formData.company">
                    </div>                

                    <div class="flex flex-row gap-x-5 items-center my-3 mx-2" v-if="(formData.is_agent && (is_admin || is_vendor))">
                        <div v-for="(m, index) in formData.margin" :key="index">
                            <label class="uppercase block text-sm font-medium text-gray-700 ">margen - {{ m.name }}</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formData.margin[index].agent_margin">                            
                        </div>                        
                    </div>                
                </div>                   
                <!-- PASSWORD RESET OPTIONS -->
                <div class="flex flex-col border border-gray-400 rounded-md p-5 mt-3">
                    <div class="flex flex-row gap-x-3 items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Contrasea</label>
                        <input 
                        :type="(showpass) ? 'text' : 'password'" 
                        class="flex-1 border mt-1 block w-90x100 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        v-model="formData.password">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Confirmar Contrasea</label>
                        <input 
                        :type="(showpass) ? 'text' : 'password'" 
                        class="flex-1 border mt-1 block w-full border-gray-400 bg-white rounded-md shadow-sm py-2 px-3
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        v-model="repassword">
                        <div class="flex items-center gap-x-4">                    
                            <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                            <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>                                                
                        </div>                        
                    </div>    
                    <div class="flex justify-between">
                        <small class="text-red" v-show="!samepass">Las contraseas deben ser iguales.</small>               
                        <!-- <small class="text-green" v-show="samepass">Las contraseas deben ser iguales.</small>                -->
                        <button type="button" class="ml-auto">Resetear Contrasea</button>
                    </div>
                </div>                

            </div>            

            <!-- Button -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">                
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>  
            </div>  
        </form>
    </div>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import { MultiSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import Authorization from "@/services/user/auth.service";
import GenderService from "@/services/common/gender.service";
import CountryService from "@/services/common/country.service";
import ClientService from "@/services/user/client.service";
import RolesService from '@/services/user/roles.service';
import ProductTypeService from '../../../services/common/product_type.service';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudClient",
    components: {
        ModelSelect,
        MultiSelect,
    },
    data() {
        return {
            showForm: true,
            info_message: '',
            error_message_text: 'Error enviando informacion. No se pudo crear el nuevo usuario',
            error_message: false,
            success_message: false,
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            load: false,
            showpass: false,
            samepass: true,
            repassword: '',
            countries: [],
            genders: [],
            roles: [],            
            formData: {
                first_name: '',
                last_name: '',
                phone: '',
                email: '',
                parent: '',
                parent_id: '',
                gender_id: '',
                gender: {
                    value: '',
                    text: ''
                },
                password: '',                
                //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
                //agency
                iata: '',
                company: '',
                is_agent: false,                
                country_id: '',
                active: 0,
                verify: 0,
                country: {
                    value: '',
                    text: ''
                },
                roles: [],
                margin: []
            },
        }
    },
    created() {
        // Init country select
        let data =[]
        data.push(CountryService.getSimpleList())//.then(c => this.parents = c)
        data.push(RolesService.getSimpleList())//.then(c => this.countries = c)
        data.push(ProductTypeService.getCustomList())//.then(c => this.countries = c)
        this.clearForm()
        Promise.all(data).then(d => {            
            this.countries = d[0]
            this.roles = d[1]
            this.formData.margin = d[2]
            this.validateInitInfo()
        })  
        
        if(this.mode != 'create') {             
            this.initForm()
        }
        // Init gender select
        GenderService.getSimpleList().then(c => this.genders = c)
    },     
    methods: {   
        validateInitInfo() {
            if(this.roles.length == 0 || this.countries.length == 0 ) {
                this.showForm = false
                this.info_message = (this.roles.length == 0) ? 'Roles <br>'  : ''
                this.info_message += (this.countries.length == 0) ? ' Paises <br>' : ''                
            }
        },         
        initForm() {
            // For upd action
            if (this.mode != 'create') {
                ClientService.getClient(this.info).then(e => {        
                    this.clearForm()                         
                    this.formData.first_name = e.first_name
                    this.formData.last_name = e.last_name
                    this.formData.phone = e.phone
                    this.formData.email = e.email
                    this.formData.parent = (e.parent) ? e.parent.first_name + ' ' + e.parent.last_name : ''
                    this.formData.parent_id = (e.parent) ? e.parent.id : ''
                    this.formData.address = e.address
                    this.formData.city = e.city
                    this.formData.province = e.province
                    this.formData.zip_code = e.zip_code
                    this.formData.iata = e.i_a_t_a
                    this.formData.company = e.company
                    this.formData.is_agent = e.is_travel_agency
                    this.formData.gender_id = e.gender_id
                    this.formData.gender = e.gender_id
                    this.formData.country_id = e.country_id
                    this.formData.country = e.country_id                    
                    this.formData.active = e.active
                    this.formData.verify = e.verified
                    for(let r of e.roles) {
                        this.formData.roles.push({
                            text: r.role_name,
                            value: r.id
                        })
                    }   
                }).catch(err => {
                    console.log(err)
                    // this.$router.push({name:'Login'})  
                    this.$router.go(0)
                })
            } else {                
                if(Authorization.validateRole(['ROLE_Agency'], true)) {
                    this.formData.is_agent = true
                    this.formData.parent = localStorage.getItem('id')
                    this.formData.roles = this.roles.filter(role => role.text == 'ROLE_Vendor')                    
                }
            }
        },   
        comparePass() {
            this.samepass = (this.password != this.repassword)
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        }, 
        onRoleSelect(items, litem){                       
            this.formData.roles = items            
        },       
        submitForm(e) {
            e.preventDefault();            
            if(this.validate(this.formData.first_name) || this.validate(this.formData.last_name) || this.validate(this.formData.gender) ||
            this.validate(this.formData.email) || this.validate(this.formData.country)) {
                
                const data = {       
                    first_name: this.formData.first_name,
                    last_name: this.formData.last_name,
                    phone: this.formData.phone,
                    email: this.formData.email,
                    parent: this.formData.parent,
                    parent_id: (typeof this.formData.parent_id == 'object') ? this.formData.parent_id.value : this.formData.parent_id,
                    gender_id: (typeof this.formData.gender == 'object') ? this.formData.gender.value : this.formData.gender,
                    password: this.formData.password,
                    address: this.formData.address,
                    city: this.formData.city,
                    province: this.formData.province,
                    zip_code: this.formData.zip_code,
                    iata: this.formData.iata,
                    company: this.formData.company,
                    is_agent: this.formData.is_agent,
                    country_id: (typeof this.formData.country == 'object') ? this.formData.country.value : this.formData.country,
                    active: this.formData.active,
                    verify: this.formData.verify,
                    roles: this.formData.roles,
                    margin: this.formData.margin,
                }
                if('id' in this.formData) data.id = this.formData.id
                ClientService.addUpdClient(data, (this.mode == 'create')).then(res => {                    
                    if(res) {
                        this.formData.password = ''
                        this.repassword = ''
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getClient(e) {
            console.log(e)
        },
        clearForm() {
            return {
                first_name: '',
                last_name: '',
                phone: '',
                email: '',
                parent: '',
                parent_id: '',
                gender_id: '',
                gender: {
                    value: '',
                    text: ''
                },
                password: '',                
                //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
                //agency
                iata: '',
                company: '',
                is_agent: '',                
                country_id: '',
                country: {
                    value: '',
                    text: ''
                },
                roles: [],
                margin: []
            }
        }
    },
    watch: {
        mode() {
            this.initForm()
        },
        repassword() {
            this.samepass =  (this.repassword == this.formData.password || 
                (this.repassword == '' && this.formData.password == ''))
        },             
    },
    computed: {
        is_admin() {
            return Authorization.validateRole(['ROLE_Admin'])    
        },
        is_agency() {
            return Authorization.validateRole(['ROLE_Agency'], true)    
        },        
        is_vendor() {            
            return (Authorization.validateRole(['ROLE_Agency'], true) && localStorage.getItem('email') != this.formData.email)            
        }
    },
}
</script>
<style >
    
</style>