<template>
    <div>
        <div class="mt-2">
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">

                <!-- Header con informacin principal -->
                <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                    
                    <!-- Referencia -->
                    <div class="flex flex-col">
                        <label class="uppercase block text-sm font-medium text-gray-700 mb-1">Referencia</label>
                        <div class="border border-gray-300 bg-gray-50 rounded-md py-1 px-2">
                            <span class="text-lg font-mono font-bold text-gray-800">{{ formData.reference }}</span>
                        </div>
                    </div>

                    <!-- Estado (Editable) -->
                    <div class="flex flex-col">
                        <label class="uppercase block text-sm font-medium text-gray-700 mb-1">Estado</label>
                        <select v-model="formData.status"                                 
                                class="border border-gray-300 rounded-md py-2 px-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
                                :class="statusClass">
                            <option v-for="status in statusOptions" 
                                    :key="status.value" 
                                    :value="status.value"
                                    :class="status.class">
                                {{ status.text }}
                            </option>
                        </select>
                    </div>
                    
                </div>

                <!-- Items del Pedido -->
                <div class="border-t border-gray-300 my-4 pt-4">
                    <h3 class="text-lg font-semibold text-gray-800 mb-4 flex items-center">
                        <i class="fa fa-shopping-cart mr-2 text-blue-500"></i>
                        Items del Pedido ({{ totalItems }} productos)
                    </h3>

                    <div v-if="formData.items && formData.items.length > 0" class="space-y-2">
                        <!-- Header de la tabla -->
                        <div class="grid grid-cols-12 gap-2 px-3 py-2 bg-gray-100 rounded-lg text-sm font-medium text-gray-700">
                            <div class="col-span-5">Producto</div>
                            <div class="col-span-2 text-center">Cantidad</div>
                            <div class="col-span-2 text-right">P. Unitario</div>
                            <div class="col-span-3 text-right">Subtotal</div>
                        </div>
                        
                        <!-- Items -->
                        <div v-for="(item, index) in formData.items" :key="index"                                
                            class="grid grid-cols-12 gap-2 px-3 py-3 border border-gray-200 rounded-lg bg-white hover:bg-gray-50 transition-colors">                            
                            <!-- Nombre del producto -->
                            <div class="col-span-5">
                                <h4 class="font-medium text-gray-800">{{ item.name }}</h4>
                                <div v-if="item.discount > 0" class="flex items-center mt-1">
                                    <span class="text-xs bg-red-100 text-red-600 px-2 py-1 rounded-full">
                                        <i class="fa fa-tag mr-1"></i>
                                        Descuento: ${{ item.discount.toFixed(2) }}
                                    </span>
                                </div>
                            </div>
                            
                            <!-- Cantidad -->
                            <div class="col-span-2 text-center">
                                <span class="inline-block bg-blue-100 text-blue-700 px-3 py-1 rounded-full text-sm font-medium">
                                    {{ item.quantity }}
                                </span>
                            </div>
                            
                            <!-- Precio Unitario -->
                            <div class="col-span-2 text-right">
                                <!-- <div class="text-sm text-gray-600">Unitario</div> -->
                                <div class="text-base font-semibold text-gray-800">
                                    ${{ (item.price_unit || 0).toFixed(2) }}
                                </div>
                            </div>
                            
                            <!-- Subtotal -->
                            <div class="col-span-3 text-right">
                                <!-- <div class="text-sm text-gray-600">Total</div> -->
                                <div class="text-lg font-bold text-green-600">
                                    ${{ (item.final_price || 0).toFixed(2) }}
                                </div>
                            </div>
                        </div>

                        <!-- Resumen financiero -->
                        <div class="bg-gray-50 border flex flex-row gap-4 justify-around mt-6 p-4 rounded-lg">
                            <!-- Subtotal -->
                            <div class="text-center">
                                <div class="text-sm text-gray-600">Subtotal</div>
                                <div class="text-xl font-bold text-gray-800">${{ subtotal.toFixed(2) }}</div>
                            </div>
                            
                            <!-- Descuentos -->
                            <div class="text-center" v-if="totalDiscount > 0">
                                <div class="text-sm text-gray-600">Descuentos</div>
                                <div class="text-lg font-semibold text-red-600">-${{ totalDiscount.toFixed(2) }}</div>
                            </div>

                            <!-- Delivery Cost -->
                            <div class="text-center" v-if="formData.delivery > 0">
                                <div class="text-sm text-gray-600">Transportacin</div>
                                <div class="text-lg font-semibold text-red-600">${{ formData.delivery.toFixed(2) }}</div>
                            </div>
                            
                            <!-- Total -->
                            <div class="text-center">
                                <div class="text-sm text-gray-600">Total General</div>
                                <div class="text-2xl font-bold text-green-600">${{ formData.amount ? formData.amount.toFixed(2) : '0.00' }}</div>
                            </div>
                        </div>
                    </div>
                    
                    <div v-else class="text-center py-8 text-gray-500 bg-gray-50 rounded-lg">
                        <i class="fa fa-shopping-cart text-4xl mb-3 text-gray-300"></i>
                        <p class="text-lg">No hay items en este pedido</p>
                    </div>
                </div>

                <!-- Informacin del Cliente y Entrega -->
                <div class="grid grid-cols-1 lg:grid-cols-2 gap-6 mt-6">
                    <!-- Informacin del Comprador -->
                    <div class="border border-gray-200 rounded-lg p-4 bg-white">
                        <h3 class="text-lg font-semibold text-gray-800 mb-4 flex items-center">
                            <i class="fa fa-user mr-2 text-blue-500"></i>
                            Informacin del Comprador
                        </h3>
                        
                        <div class="space-y-3">
                            <div class="flex items-center">
                                <i class="fa fa-id-card text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Nombre</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_user.first_name }} {{ formData.marketplace_user.last_name }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center">
                                <i class="fa fa-envelope text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Email</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_user.email }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center">
                                <i class="fa fa-phone text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Telfono</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_user.phone }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center" v-if="formData.marketplace_user.address">
                                <i class="fa fa-map-marker text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Direccin</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_user.address }}</div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <!-- Informacin de Entrega -->
                    <div class="border border-gray-200 rounded-lg p-4 bg-white">
                        <h3 class="text-lg font-semibold text-gray-800 mb-4 flex items-center">
                            <i class="fa fa-truck mr-2 text-green-500"></i>
                            Informacin de Entrega
                        </h3>
                        
                        <div class="space-y-3">
                            <div class="flex items-center">
                                <i class="fa fa-user text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Contacto</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_contact.name }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center">
                                <i class="fa fa-id-card text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">CI</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_contact.id_number }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center">
                                <i class="fa fa-phone text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Telfono</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_contact.phone }}</div>
                                </div>
                            </div>
                            
                            <div class="flex items-center">
                                <i class="fa fa-map-marker text-gray-400 w-6"></i>
                                <div class="ml-3">
                                    <div class="text-sm text-gray-500">Direccin de entrega</div>
                                    <div class="font-medium text-gray-800">{{ formData.marketplace_contact.address }}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- Espacio para datos futuros -->
                <div class="border-t border-gray-300 my-6 pt-6">
                    <h3 class="text-lg font-semibold text-gray-800 mb-4 text-center">
                        Informacin Adicional
                    </h3>
                    <div class="text-center text-gray-500 italic">
                        <i class="fa fa-info-circle mr-2"></i>
                        Espacio reservado para transporte, ofertas y otros datos del pedido
                    </div>
                </div>

            </div>

            <!-- Botones -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="button" 
                        class="inline-flex border border-transparent bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                            hover:bg-green-500 focus:outline-none 
                            focus:border-green-700 focus:ring focus:ring-green-200
                            active:bg-green-600 
                            disabled:opacity-25 transition"
                        :disabled="load"
                        @click="submitForm">
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>              
                <button type="button" 
                        class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                            hover:text-white 
                            focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                            active:text-gray-800 active:bg-red-50 
                            disabled:opacity-25 transition"
                        :disabled="load"
                        @click="cancelForm">
                    Cancelar
                </button> 
                
                <!-- Mensajes de error y xito -->
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ error_message_text }}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ success_message_text }}</h5>
                </div>                           
            </div>

        </div>
    </div>
</template>

<script>
import MarketplaceOrderService from "@/services/marketplace/marketplace_order.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudMarketplaceOrder",
    data() {
        return {
            load: false,
            error_message: false,
            error_message_text: '',
            success_message: false,
            success_message_text: '',
            formData: {
                id: '',
                user_id: '',
                status: '',
                reference: '',
                order_date: '',
                contact_id: '',
                amount: 0,
                delivery: 0,
                created_at: '',
                updated_at: '',
                items: [],
                marketplace_user: {
                    id: '',
                    username: '',
                    first_name: '',
                    last_name: '',
                    email: '',
                    address: '',
                    phone: '',
                    zipcode: ''
                },
                marketplace_contact: {
                    id: '',
                    user_id: '',
                    city_id: '',
                    name: '',
                    id_number: '',
                    address: '',
                    phone: ''
                }
            }
        }
    },
    computed: {
        // Opciones del select basadas en el servicio
        statusOptions() {
            return MarketplaceOrderService.status_list.map(status => ({
                value: status.value,
                text: status.text,
                class: this.getStatusClass(status.value)
            }));
        },
        
        totalItems() {
            if (!this.formData.items) return 0;
            return this.formData.items.reduce((total, item) => total + (item.quantity || 0), 0);
        },
        subtotal() {
            if (!this.formData.items) return 0;
            return this.formData.items.reduce((total, item) => total + (item.price_unit * item.quantity || 0), 0);
        },
        totalDiscount() {
            if (!this.formData.items) return 0;
            return this.formData.items.reduce((total, item) => total + (item.discount || 0), 0);
        },
        statusClass() {
            return this.getStatusClass(this.formData.status);
        }
    },
    created() {
        this.initForm();
    },
    watch: {
        mode() {
            this.initForm();
        }
    },
    methods: {
        // Mapeo centralizado de clases para los estados
        getStatusClass(status) {
            const statusClassMap = {
                'created': 'bg-indigo-300 text-white border-blue-200',      // En el carrito
                'sended': 'bg-yellow-300 text-yellow-800 border-yellow-200', // Pendiente
                'completed': 'bg-green-300 text-green-800 border-green-200',    // Pagado
                'cancelled': 'bg-pink-400 text-white border-red-200'          // Cancelado
            };
            return statusClassMap[status] || 'bg-gray-100 text-gray-800 border-gray-200';
        },

        // Mtodo para obtener texto del status
        getStatusText(status) {
            return MarketplaceOrderService.getStatusText(status);
        },

        initForm() {            
            this.load = true;
            MarketplaceOrderService.getMarketplaceOrder(this.info)
                .then(item => {   
                    console.log(item);                 
                    if (this.mode != 'create') {
                        this.formData = item;
                        if (!this.formData.items) {
                            this.formData.items = [];
                        } else {
                            this.formData.items = this.formData.items.filter(item => item.quantity > 0);
                        }
                    }
                })
                .catch(error => {
                    console.error('Error loading order:', error);
                    this.error_message = true;
                    this.error_message_text = 'Error al cargar el pedido';
                }).finally(() => { 
                    this.load = false;
                });
        },

        submitForm(e) {            
            e.preventDefault();
            this.error_message = false;
            this.success_message = false;
            
            this.load = true;
            
            const updateData = {
                id: this.formData.id,
                status: this.formData.status
            };
            
            MarketplaceOrderService.updateMarketplaceOrder(updateData)
                .then(response => {
                    this.success_message = true;
                    this.success_message_text = 'Estado actualizado correctamente';
                    setTimeout(() => {
                        this.success_message = false;
                    }, 3000);
                })
                .catch(error => {
                    this.error_message = true;
                    this.error_message_text = 'Error al actualizar el estado del pedido';
                    setTimeout(() => {
                        this.error_message = false;
                    }, 3000);
                })
                .finally(() => {
                    this.load = false;
                });
        },        
        cancelForm() {
            this.$emit('close');
        },
    }
}
</script>