import Authorization from "../user/auth.service";

const base_url = "marketplace-items";
const MarketplaceItemService = {
  getList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {
            const option = {
              id: item.id,
              alias: item.alias,
              stock: item.dispo.stock,
              in_offer: item.in_offer ? true : false,
              delivery_free: item.delivery_free ? true : false,
              active: item.active ? true : false,
              price: item.price,
            };

            list.push(option);
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },
  getSimpleList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {
            if (item.id !== 1) {
              const option = {
                value: item.id,
                text: item.alias,
              };

              list.push(option);
            }
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },
  /* getSimpleList(){

    }, */
  async getMarketplaceItem(id) {
    //! Obtiene los datos x un ID
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`, { auth: "bearer" });
      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
    }
  },
  async addUpdMarketplaceItem(form_data, is_new = true) {
    try {
      // console.log(form_data);
      form_data.auth = "bearer";
      const { status, data } = is_new
        ? await Authorization.checkLoginImage(`/${base_url}/create`, form_data.form, "post")
        : await Authorization.checkLoginImage(`/${base_url}/upd/${form_data.id}`, form_data.form, "put");
      if (status === 200) {
        return data;
      }
    } catch (e) {
      //! Fail
      console.error(e.response);
    }
    return false;
  },
  async delete(ids) {
    const delete_data = {
      auth: "bearer",
      ids,
    };
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, "post");
      if (status == 200) {
        return this.getList().then((list) => {
          return list;
        });
      }
    } catch (e) {
      //! Fail
      console.error(e.response);
    }
  },
};

export default MarketplaceItemService;
