import Authorization from "../user/auth.service";

const PoleService = {
    getList() {
        try {
            return Authorization.checkLogin(`/poles`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            active: (item.active) ? true : false,
                            keyword: item.keyword,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.alias
                })
            }
            return result
        })

    },
    async getPole(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/poles/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdPole(pole_data, is_new = true, hasImage = false) {
        try {            
            pole_data.auth = 'bearer'                  
            const url = (is_new) ? '/poles/create' : `/poles/upd/${pole_data.id}`,
                method = (is_new) ? 'post' : 'put'

            const {status, data} = (hasImage) ? 
                await Authorization.checkLogin(url, pole_data, method) :
                await Authorization.checkLoginImage(url, pole_data.form, method)
                
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/poles/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default PoleService