<template>
    <div>
        <div class="mt-2">
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">

                <!-- Header con informacin principal -->
                <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mb-6">
                    
                    <!-- Provincia -->
                    <div class="flex flex-col">
                        <label class="uppercase block text-sm font-medium text-gray-700 mb-1">Provincia</label>
                        <model-select
                            :options="provinceOptions"
                            v-model="formData.province_id"
                            placeholder="Seleccione la provincia"                       
                            required
                            @searchchange="filterProvinces"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white text-gray-800 rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                        </model-select>
                    </div>

                    <!-- Mximo de items -->
                    <div class="flex flex-col">
                        <label class="uppercase block text-sm font-medium text-gray-700 mb-1">Mximo de Items</label>
                        <input type="number" 
                               v-model="formData.max_items"
                               class="border border-gray-300 rounded-md py-2 px-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
                               min="0">
                    </div>

                    <!-- Precio -->
                    <div class="flex flex-col">
                        <label class="uppercase block text-sm font-medium text-gray-700 mb-1">Precio</label>
                        <input type="number" 
                               v-model="formData.price"
                               step="0.01"
                               class="border border-gray-300 rounded-md py-2 px-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
                               min="0">
                    </div>
                    
                </div>

            </div>

            <!-- Botones -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="button" 
                        class="inline-flex border border-transparent bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                            hover:bg-green-500 focus:outline-none 
                            focus:border-green-700 focus:ring focus:ring-green-200
                            active:bg-green-600 
                            disabled:opacity-25 transition"
                        :disabled="load"
                        @click="submitForm">
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>              
                <button type="button" 
                        class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                            hover:text-white 
                            focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                            active:text-gray-800 active:bg-red-50 
                            disabled:opacity-25 transition"
                        :disabled="load"
                        @click="cancelForm">
                    Cancelar
                </button> 
                
                <!-- Mensajes de error y xito -->
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ error_message_text }}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ success_message_text }}</h5>
                </div>                           
            </div>

        </div>
    </div>
</template>

<script>
import MarketplaceDeliveryService from "@/services/marketplace/marketplace_delivery.service";
import MarketplaceProvinceService from "@/services/marketplace/marketplace_province.service";
import { ModelSelect } from 'vue-search-select';

export default {
    components: {
        ModelSelect
    },
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudMarketplaceDelivery",
    data() {
        return {
            load: false,
            error_message: false,
            error_message_text: '',
            success_message: false,
            success_message_text: '',
            provinceOptions: [], // Opciones para el select
            allProvinces: [], // Todas las provincias (para filtrado)
            formData: {
                id: '',
                province_id: '',
                max_items: 0,
                price: 0,                
            }
        }
    },
    computed: {
        // Computed para obtener el nombre de la provincia seleccionada
        selectedProvinceName() {
            return this.getProvinceName(this.formData.province_id);
        }
    },
    created() {
        this.loadProvinces();
        this.initForm();
    },
    watch: {
        mode() {
            this.initForm();
        }
    },
    methods: {
        // Cargar provincias desde el servicio
        loadProvinces() {
            this.load = true;
            MarketplaceProvinceService.getSimpleList()
                .then(items => { 
                    console.log('Provincias cargadas:', items); // Debug
                    // El servicio ya retorna el formato correcto: {value: id, text: name}
                    this.allProvinces = items;
                    this.provinceOptions = [...this.allProvinces];
                })
                .catch(error => {
                    console.error('Error loading provinces:', error);
                    this.error_message = true;
                    this.error_message_text = 'Error al cargar las provincias';
                    setTimeout(() => {
                        this.error_message = false;
                    }, 3000);
                })
                .finally(() => {
                    this.load = false;
                });
        },

        // Filtrar provincias basado en bsqueda
        filterProvinces(searchText) {
            if (!searchText) {
                this.provinceOptions = [...this.allProvinces];
                return;
            }
            
            const searchLower = searchText.toLowerCase();
            this.provinceOptions = this.allProvinces.filter(province => 
                province.text.toLowerCase().includes(searchLower)
            );
        },

        // Obtener nombre de la provincia por ID
        getProvinceName(provinceId) {
            const province = this.allProvinces.find(p => p.value == provinceId);
            return province ? province.text : 'No seleccionada';
        },

        initForm() {            
            this.load = true;
            
            if (this.mode === 'create') {
                // Inicializar formulario vaco para crear
                this.formData = {                    
                    province_id: '',
                    max_items: 0,
                    price: 0
                };
                this.load = false;
            } else {
                // Cargar datos existentes para editar
                MarketplaceDeliveryService.getMarketplaceDelivery(this.info)
                    .then(item => {   
                        console.log('Datos cargados para editar:', item); // Debug
                        this.formData = {
                            id: item.id || '',
                            province_id: item.province_id || '',
                            max_items: item.max_items || 0,
                            price: item.price || 0
                        };
                        
                        // Debug: Verificar si la provincia existe en las opciones
                        if (this.formData.province_id) {
                            const foundProvince = this.allProvinces.find(p => p.value == this.formData.province_id);
                            console.log('Provincia encontrada para el ID:', this.formData.province_id, foundProvince);
                        }
                    })
                    .catch(error => {
                        console.error('Error loading delivery config:', error);
                        this.error_message = true;
                        this.error_message_text = 'Error al cargar la configuracin de entrega';
                    }).finally(() => { 
                        this.load = false;
                    });
            }
        },

        submitForm(e) {            
            e.preventDefault();
            this.error_message = false;
            this.success_message = false;
            
            // Validaciones bsicas
            if (!this.formData.province_id) {
                this.error_message = true;
                this.error_message_text = 'Debe seleccionar una provincia';
                return;
            }

            if (this.formData.max_items < 0) {
                this.error_message = true;
                this.error_message_text = 'El mximo de items no puede ser negativo';
                return;
            }

            if (this.formData.price < 0) {
                this.error_message = true;
                this.error_message_text = 'El precio no puede ser negativo';
                return;
            }
            
            this.load = true;
            
            const submitData = {
                id: this.formData.id,
                province_id: this.formData.province_id,
                max_items: this.formData.max_items,
                price: this.formData.price
            };
            
            console.log('Datos a enviar:', submitData); // Debug
            
            const submitPromise = this.mode === 'create' 
                ? MarketplaceDeliveryService.addUpdateMarketplaceDelivery(submitData)
                : MarketplaceDeliveryService.addUpdateMarketplaceDelivery(submitData,false);
            
            submitPromise
                .then(response => {
                    this.success_message = true;
                    this.success_message_text = this.mode === 'create' 
                        ? 'Configuracin creada correctamente' 
                        : 'Configuracin actualizada correctamente';
                    setTimeout(() => {
                        this.success_message = false;
                        this.$emit('close');
                    }, 2000);
                })
                .catch(error => {
                    this.error_message = true;
                    this.error_message_text = this.mode === 'create' 
                        ? 'Error al crear la configuracin' 
                        : 'Error al actualizar la configuracin';
                    setTimeout(() => {
                        this.error_message = false;
                    }, 3000);
                })
                .finally(() => {
                    this.load = false;
                });
        },        
        cancelForm() {
            this.$emit('close');
        },
    }
}
</script>