<template >
    <div>
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores: <br><br><b v-html="info_message"></b></span>
        </div>
        <form id="hotelForm" action="" class="mt-2" v-if="showForm" enctype="multipart/form-data"
         @submit="submitForm">             
            <tabs>
                <tab name="General">      
                    <!-- <map-zone :latlng="[formData.latitude, formData.longitude]"></map-zone>                     -->
                    <!-- General -->
                    <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                        <div class="flex flex-row items-center my-3 mx-2">            
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Activo</label>
                            <input type="checkbox" class="p-2" v-model="formData.active">
                        </div>                            
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Alias *</label>
                            <input type="text" required
                            v-model="formData.alias"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">           
                        </div>   
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">keyword *</label>                                
                            <input type="text" required
                            v-model="formData.keyword"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " 
                            >            
                        </div>  
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">orden</label>
                            <input type="text" 
                            v-model="formData.order"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">            
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">servicios de mayordomo</label>
                            <input type="checkbox" class="p-2" v-model="formData.handicapped_services">
                        </div>                                                                     
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">ao de construccin</label>
                            <input type="text" 
                            v-model="formData.build_year"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">restaurado en</label>
                            <input type="text" 
                            v-model="formData.restored_year"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">edificios</label>
                            <input type="text" 
                            v-model="formData.buildings"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">casas</label>
                            <input type="text" 
                            v-model="formData.houses"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">bungalows</label>
                            <input type="text" 
                            v-model="formData.bungalows"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">habitaciones</label>
                            <input type="text" 
                            v-model="formData.rooms"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>                                                
                        <div class="flex flex-row gap-4 flex-wrap">   
                            <div class="flex flex-row items-center my-3 mx-2">            
                                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">permite nios</label>
                                <input type="checkbox" class="p-2" v-model="formData.children_allowed">
                            </div>
                        </div>                               
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">cadena</label>
                            <model-select :options="chains"
                                    v-model="formData.chain"
                                    placeholder="Seleccione la cadena *"
                                    required
                                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </model-select>               
                        </div>                   
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">plan alimenticio</label>
                            <model-select :options="categories"
                                    v-model="formData.category"
                                    placeholder="Seleccione el Polo *"
                                    required
                                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </model-select>   
                        </div>                        
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">segmentos</label>
                            <multi-select :options="segments"
                                :selected-options="formData.segments_id"
                                placeholder="Seleccione los segmentos *"       
                                @select="onSegmentSelect"                                 
                                required
                                class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </multi-select>                                         
                        </div>                         
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">estrellas</label>            
                            <input type="number" min="1" max="5" v-model="formData.stars"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " 
                            >            
                        </div>                
                    </div>

                    
 
                </tab>
                <tab name="Descripcin">
                    <div class="flex flex-row items-center my-3 mx-2">                        
                        <textarea cols="30" rows="25" placeholder="Escriba la descripcin del hotel"
                        v-model="formData.description"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        </textarea>
                    </div>                        
                </tab>
                <tab name="Contactos">
                    <!-- Contact -->
                    <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                        
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Telfono</label>
                            <input type="text" 
                            v-model="formData.phone"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            >            
                        </div>                        
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">link</label>
                            <input type="url" 
                            v-model="formData.url"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " 
                            >            
                        </div>                            
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">email</label>
                            <input type="email" 
                            v-model="formData.email"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " 
                            >            
                        </div>
                          
                        
                    </div> 
                </tab> 
                <tab name="Locacin">
                    <!-- Location -->
                    <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Polo *</label>            
                            <model-select :options="poles"
                                    v-model="formData.pole"
                                    placeholder="Seleccione el Polo *"
                                    required
                                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </model-select>             
                        </div>                
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Locacin</label>
                            <model-select :options="locations"
                                    v-model="formData.location"
                                    placeholder="Seleccione el Polo *"
                                    required
                                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                    focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </model-select>  
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Direccin</label>
                            <input type="text" 
                            v-model="formData.address"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        </div>                        
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Longitud</label>
                            <input type="text" 
                            v-model="formData.longitude"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Latitud</label>
                            <input type="text" 
                            v-model="formData.latitude"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                        </div>
                    </div>
                </tab> 
                <tab name="Relacionado al precio">
                    <!-- Offertas -->
                    <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                                        
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">en oferta especial</label>
                            <input type="checkbox" class="p-2" v-model="formData.in_special_offer">                    
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">precio desde</label>
                            <input type="text" 
                            v-model="formData.price_from"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">           
                        </div>  
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">check_in</label>
                            <input type="text" 
                            v-model="formData.check_in"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">check_out</label>
                            <input type="text" 
                            v-model="formData.check_out"
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                       
                        </div>                
                    </div>
                </tab>                                             
                <tab name="Habitaciones" v-if="(mode != 'create')">      
                    <admin-table :rows="hotelrooms" :actions="{show: false}" :deleteCols="[]" v-on:editData="editRoom" v-if="hotelrooms.length > 0"></admin-table>
                    <div class="info-message flex item-center p-4 rounded-xl" :class="{'hidden': hotelrooms.length > 0}">
                        <i class="fas fa-exclamation-circle"></i>
                        <span>El hotel no tiene asignada ninguna habitacin. Vaya al men "Habitacin Hotel" y asigne las habitaciones a los hoteles</span>
                    </div>
                </tab>   
                <tab name="Suplementos">
                    <div class="flex gap-6 my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                        <button type="button" 
                        class="self-start mt-4 mb-auto border border-gray-400 bg-lightBlue rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2"
                        @click="addSuplement"><i class="fa fa-plus"></i> Agregar</button>                            
                        <suplement :sup="suplements" :list="formData.suplements"
                        v-on:delete="delSuplement"
                        v-on:upd="updSuplement"
                        ></suplement>
                    </div>
                </tab>        
                <tab name="Galeria">
                    <div class="px-4">
                        <button type="button" class="border border-gray-400 bg-lightBlue rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2"
                        @click="addImage">
                            <i class="fa fa-plus"></i>
                            Agregar imagen
                        </button>
                    </div>
                    <div class="flex gap-3" v-for="(img,index) in galleries" :key="index">
                        <add-images class="w-95" :index="index" :info="img" v-on:imgStatus="updImage"></add-images>
                        <button type="button" class="text-white bg-red rounded-2xl py-2 px-4 my-auto"
                        @click="removeImage(index)"><i class="fa fa-times"></i></button>
                    </div>
                </tab>        
            </tabs>
            <!-- Button -->            

            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">                
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>  
            </div>    
        </form>        
    </div>
</template>
<script>
import moment from 'moment'
import Suplement from '@/components/Suplement'
import MapZone from '@/components/Maps/MapZone'
import {Tabs, Tab} from 'vue-tabs-component'
import '@/assets/css/tabs.css'

import AddImages from '@/components/AddImages';
import AdminTable from '@/components/Admin/AdminTable';
import '@/assets/css/table.css'

import { ModelSelect } from 'vue-search-select'
import { MultiSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import PoleService from "@/services/pole/pole.service";
import HotelService from "@/services/hotel/hotel.service"
import HotelCategoryService from "@/services/hotel/hotel_category.service"
import HotelChainService from "@/services/hotel/hotel_chain.service"
import HotelLocationService from "@/services/hotel/hotel_location.service"
import HotelSegmentService from "@/services/hotel/hotel_segment.service"
import HotelSuplementService from "@/services/hotel/hotel_suplement.service"

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudHotel",    
    components: {
        Tabs,
        Tab,
        AdminTable, 
        ModelSelect,
        MultiSelect,
        MapZone,
        Suplement,
        AddImages
    },
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la habitacin correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true,
        info_message: '',
        poles: [],
        categories: [],
        chains: [],
        locations: [],
        hotelrooms: [],
        segments: [],
        suplements: [],        
        formData: {},       
        //Gallery vars
        galleries: [],
        img_num: 0 
    }),
    created() { 
        let data =[]
        data.push(PoleService.getSimpleList())//.then(c => this.poles = c)
        data.push(HotelCategoryService.getSimpleList())//.then(c => this.categories = c)
        data.push(HotelChainService.getSimpleList())//.then(c => this.chains = c)
        data.push(HotelLocationService.getSimpleList())//.then(c => this.locations = c)
        data.push(HotelSegmentService.getSimpleList())//.then(c => this.segments = c)      
        data.push(HotelSuplementService.getSimpleList())//.then(c => this.segments = c)      
        
        Promise.all(data).then(d => {
            // console.log(d)
            this.poles = d[0]
            this.categories = d[1]
            this.chains = d[2]
            this.locations = d[3]
            this.segments = d[4]
            this.suplements = d[5]
            
            this.validateInitInfo()
        }) 
        this.clearForm()
        
        if(this.mode != 'create') { 
            HotelService.getRoomList(this.info).then(c => {            
                for(let r of c) {
                    this.hotelrooms.push({
                        id: r.id,
                        alias: r.alias,
                        active: (r.active) ? true : false,
                        order: r.order
                    })
                }
            }) 
            this.initForm()
        }
    },    
    methods:{
        validateInitInfo() {
            if(this.poles.length == 0 || this.categories.length == 0 || this.chains.length == 0 ||
            this.locations.length == 0 || this.segments.length == 0 || this.suplements.length == 0 ) {
                this.showForm = false
                this.info_message = (this.poles.length == 0) ? 'Polos <br>'  : ''
                this.info_message += (this.categories.length == 0) ? ' Caegorias de hotel <br>' : ''
                this.info_message += (this.chains.length == 0) ? ' Cadenas hoteleras <br>' : ''
                this.info_message += (this.locations.length == 0) ? ' Locaciones <br>' : ''
                this.info_message += (this.segments.length == 0) ? ' Segmentos <br>' : ''
                this.info_message += (this.suplements.length == 0) ? ' Suplementos <br>' : ''
            }
        },
        initForm() {
            HotelService.getHotel(this.info).then(e => {
                this.clearForm()
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.pole = e.pole_id
                    this.formData.pole_id = e.pole_id
                    this.formData.location = e.location_id
                    this.formData.location_id = e.location_id
                    this.formData.chain = e.chain_id
                    this.formData.chain_id = e.chain_id
                    this.formData.category = e.category_id
                    this.formData.category_id = e.category_id
                    this.formData.stars = e.stars
                    this.formData.keyword = e.keyword
                    this.formData.description = e.description
                    this.formData.logo = e.logo
                    this.formData.order = e.order
                    this.formData.address = e.address
                    this.formData.in_special_offer = e.in_special_offer
                    this.formData.phone = e.phone
                    this.formData.email = e.email
                    this.formData.price_from = e.price_from
                    this.formData.build_year = e.build_year
                    this.formData.restored_year = e.restored_year
                    this.formData.url = e.url
                    this.formData.buildings = e.buildings
                    this.formData.houses = e.houses
                    this.formData.bungalows = e.bungalows
                    this.formData.rooms = e.rooms
                    this.formData.handicapped_services = e.handicapped_services
                    this.formData.children_allowed = e.children_allowed
                    this.formData.check_in = e.check_in
                    this.formData.check_out = e.check_out
                    this.formData.active = e.active
                    this.formData.in_offer = 1
                    if(e.hasOwnProperty('hotel_suplements')) {
                        for(let su of e.hotel_suplements) {                        
                            this.formData.suplements.push({
                                suplement: su.suplement,
                                price: su.price 
                            })
                        }                                     
                    }
                    for(let s of e.hotel_segments) {                        
                        this.formData.segments.push({
                            text: s.segment,
                            value: s.segment_id
                        })
                    }
                    for(let s of e.hotel_segments) {                        
                        this.formData.segments_id.push({
                            text: s.segment,
                            value: s.segment_id
                        })
                    }                
                    if(e.images)    
                        this.initGallery(e.images)
                }
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias) && this.validate(this.formData.pole) &&
            this.validate(this.formData.location) && this.validate(this.formData.chain)
            && this.validate(this.formData.category) && this.validate(this.formData.keyword) ) {   
                // Fix segment list                
                const formData = this.formData
                const segment_list = []
                for(let s of this.formData.segments_id){
                    segment_list.push(s.value)
                }    
                this.formData.suplements = JSON.stringify(this.formData.suplements)
                this.formData.segments = segment_list
                this.formData.price_from = (this.formData.price_from == '') ? 0 : this.formData.price_from                
                this.formData.pole_id = (typeof this.formData.pole == 'object') ? this.formData.pole.value : this.formData.pole
                this.formData.location_id = (typeof this.formData.location == 'object') ? this.formData.location.value : this.formData.location
                this.formData.chain_id = (typeof this.formData.chain == 'object') ? this.formData.chain.value : this.formData.chain
                this.formData.category_id = (typeof this.formData.category == 'object') ? this.formData.category.value : this.formData.category
                this.formData.active = (this.formData.active == '') ? 0 : 1
                this.formData.handicapped_services = (this.formData.handicapped_services == '') ? 0 : 1
                this.formData.children_allowed = (this.formData.children_allowed == '') ? 0 : 1
                this.formData.in_special_offer = (this.formData.in_special_offer == '') ? 0 : 1
                
                this.load = true
                // Creo el formulario para enviar las imagenes
                const fd = new FormData()                
                const orders = []
                if(this.galleries.length > 0) {
                    for(let img of this.galleries) {
                        orders.push(img.order)
                        if(img.url.includes('blob'))
                            fd.append('images', img.file, img.file.name)
                        else 
                            fd.append('images', img.id)
                    }
                    fd.append('imgOrder', orders)
                }
                
                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }

                HotelService.addUpdHotel({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    this.clearForm()
                    if(res) {                        
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        addSuplement(){
            this.formData.suplements.push({
                suplement: {
                    value: '',
                    text: ''
                },
                price: 0                
            })
        },
        updSuplement(obj) {            
            this.formData.suplements[obj.index] = obj.obj
        },
        delSuplement(index) {            
            this.formData.suplements.splice(index, 1)
        },
        onSegmentSelect(items, litem){
            this.formData.segments_id = items
        },        
        onRoomSelect(items, litem){
            this.formData.hotel_rooms = items
        },
        handeClick(newTab){
            this.currentTab = newTab;
        },
        editRoom(rowData) {
            // Showing Form      
            // this.crudvisible = !this.crudvisible          
            // const is_new = false
            // this.mode = 'update'          
            // this.formInfo = rowData.id
        },
        clearForm() {
            this.formData = {
                id: '',
                alias: '',
                pole_id: '',
                pole: {
                    value: '',
                    text: ''
                },
                location: '',
                location_id: {
                    value: '',
                    text: ''
                },
                chain: '',
                chain_id: {
                    value: '',
                    text: ''
                },
                category: '',
                category_id: {
                    value: '',
                    text: ''
                },
                segments: [],
                segments_id:[],
                suplements: [],
                stars: '',
                keyword: '',
                logo: '',
                order: '',
                address: '',
                in_special_offer: 0,
                phone: '',
                email: '',
                price_from: 0,
                build_year: '',
                restored_year: '',
                url: '',
                buildings: '',
                houses: '',
                bungalows: '',
                handicapped_services: 0,
                children_allowed: 0,
                check_in: '',
                check_out: '',
                active: 0,
                longitude: '',
                latitude: '',
                published: 0,
            }
        },
        //Gallery engine
        initGallery(images) {
            this.galleries = []
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')                
                this.galleries.push({
                    id: img.id,
                    class_name: img.class_name,            
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                })
            }
            
        },
        addImage() {
            this.galleries.push({
                id: 0,
                class_name: '',            
                id_object_class: 0,
                subcategory: '',
                lang: 'all',
                image_name: '',
                image_keyword: '',
                url: '',
                file: '',
                ext: '',
                anno: moment().format('YYYY'),
                order: this.galleries.length+1,
                active: 1,
            })
        },
        updImage(obj) {
            if(obj.index >= 0) {
                this.galleries[obj.index] = obj.img
                this.galleries[obj.index].id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },
        removeImage(index) {  
            let aux = this.galleries
            aux.splice(index,1)             
            this.galleries = aux
        },
        
    },
}
</script>