import Authorization from "../user/auth.service";

const base_url = 'marketplace-items/city'
const MarketplaceCityService = {
    getList() {
        try {
            return Authorization.checkLogin(`/${base_url}`).then(res => {
                if (res.status !== 200){
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }

                const list = [];
                for (let item of res.data) {                    
                     
                    const option = {
                        id: item.id,
                        // provincia: item.name,
                        alias: item.name,
                    };
                        
                    list.push(option);
                }
                
                return list;                
            })
            .catch (e => { console.error(e) }) 
        } catch (e) {
            console.error(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/${base_url}`/* ,{ auth: 'bearer' } */).then(res => {
                                
                if (res.status !== 200){
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }

                const list = [];
                for (let item of res.data) {
                    if (item.id !== 1) {
                     
                        const option = {
                            value: item.id,
                            text: item.name
                        };
                        
                        list.push(option);
                    }
                }
                
                return list;                
            })
            .catch (e => { console.error(e) }) 
        } catch (e) {
            console.error(e)
        }
    },
    /* getSimpleList(){

    }, */
    async getMarketplaceCity(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`,{ auth: 'bearer' })
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.error(e.response)
        }
    },
    async addUpdMarketplaceCity(form_data, is_new = true) {
        try {            
            form_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin(`/${base_url}/create`, form_data, 'post'):
                await Authorization.checkLogin(`/${base_url}/upd/${form_data.id}`, form_data, 'put')
            if (status === 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.error(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.error(e.response)
        }
    }
}



export default MarketplaceCityService
