      return {
         error_message_text: '',
         error_message: false,
         success_message: false,
         menuList: {
            client: true,        
            roles: false,
            pole: false,
            pole_type: false,        
            texts: false,        
            offers: false,        
            country: false,
            currency: false,
            place: false,
            gender: false,
            product_type: false,
            gallery: false,
            gallery_photo_filter: false,
            gallery_filter: false,
            gallery_i18n: false,
            hotel: false,        
            hotel_category: false,
            hotel_chain: false,
            hotel_room: false,
            hotel_room_allotment: false,
            hotel_location: false,
            hotel_room_type: false,
            hotel_room_type_category: false,
            hotel_segment: false,
            hotel_suplement: false,
            hotel_segment_hotel: false,
            car: false,
            car_category: false,
            car_engine: false,
            pickup_places: false,
            rate_offer: false,        
            rate_type: false,        
            hotel_price: false,
            booking: false,
            booking_status: false,
            marketplace_category: false,
            marketplace_item: false,
            marketplace_order: false,
            marketplace_delivery: false,
            marketplace_client: false,
            marketplace_province: false,
            marketplace_city: false,
         },
      }
   },
   created() {
      this.updActiveMenu(this.menu)
   },
   computed: {   
   },  
   methods: {     
      updActiveMenu(view) {
         const keys  = Object.keys(this.menuList)
         keys.map(item => {
            this.menuList[item] = false
         })
         this.menuList[view] = true         
      },
      messageInfo(info){
         if(info.type == 'error') {
         this.error_message_text = info.text
         this.error_message = true
         this.success_message = false
         } else {
         this.error_message = false
         this.success_message = true
         }
         setTimeout(() => {
         this.error_message = false
         this.success_message = false
         }, 3000);
      }, 
   }
}
</script>
