<template >
    <div>
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores: <br><br><b v-html="info_message"></b></span>
        </div>
        <form action="" method="POST" class="mt-2" enctype="multipart/form-data"
        v-if="showForm"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            
            <!-- UPLOAD Image-->
            <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">imagen</label>                    
                    <div class=""><!-- multiple :name="uploadFieldName" :disabled="isSaving" $event.target.name, $event.target.files-->
                    <input type="file" ref="file"  @change="onFileSelected"
                        accept="image/*" class="">
                </div>
            </div>

            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">activo</label>
                <input type="checkbox" class="p-2" v-model="formData.active" >
            </div>            
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Tipo Polo</label>
                    <multi-select :options="pole_types"
                                :selected-options="formData.pole_types"
                                placeholder="Seleccione los tipos de polos *"       
                                @select="onPoleTypesSelect"                                 
                                required
                                class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                            </multi-select> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Pas</label>
                    <model-select :options="countries"
                            v-model="formData.country"
                            placeholder="Seleccione Pas *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Alias</label>
                <input v-model="formData.alias" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Orden</label>
                <input v-model="formData.order" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword</label>
                <input v-model="formData.keyword" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Codsv</label>
                <input v-model="formData.codsv" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Longitud</label>
                <input v-model="formData.longitude" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Latitud</label>
                <input v-model="formData.latitude" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
        </div>
            
        <!-- Button -->        
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                hsover:bg-green-500 focus:outline-none 
                focus:border-green-700 focus:ring focus:ring-green-200
                active:bg-green-600 
                disabled:opacity-25 transition"
                :disabled="load">
                Salvar&nbsp;
                <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button> 
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                hover:text-white 
                focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                active:text-gray-800 active:bg-red 
                disabled:opacity-25 transition"
                :disabled="load"
                >Cancelar</button> 
            <div v-if="error_message" class="error-message">
                <i class="fa fa-times-circle"></i>
                <h5 class="text-xl self-center px-2">
                    Error enviando informacion. No se pudo crear el nuevo usuario
                    <p>{{error_message_text}}</p>
                </h5>
                </div>
            <div v-if="success_message" class="success-message">
                <i class="fa fa-check-circle"></i>
                <h5 class="text-xl self-center px-2">La informacin ha sido enviada satisfactoriamente.</h5>
            </div>
        </div>

        </form>
    </div> 
      

</template>
<script>
// import ChoicePointMap from '@/components/Maps/ChoicePointMap';
import CountryService from "@/services/common/country.service";
import PoleService from "@/services/pole/pole.service";
import PoleTypePoleService from "@/services/pole/pole_type_pole.service";
import { MultiSelect } from 'vue-search-select';
import FileUploadService from "@/services/fileupload.service";
import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudPole",
    components: {
        // ChoicePointMap,
        MultiSelect,
        ModelSelect,
    },
    data() {
        return {
            selectedFile: null,
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            info_message: '',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,  
            showForm: true,
            pole_types: [],
            countries: [],
            parents: [],
            formData: {
                //general info
                id: '',
                // images:[],
                order: '',
                alias: '',
                keyword: '',
                codsv: '',
                longitude: '',
                latitude: '',
                active: '',
                parent_id: '',
                parent: {
                    value: '',
                    text: ''
                },
                country_id: '',
                country: {
                    value: '',
                    text: ''
                },
                pole_types_id: [],
                pole_types: [],
            },
        }
    },
    created() {
        // Init country select
        let data =[]
        data.push(PoleService.getSimpleList())//.then(c => this.parents = c)
        data.push(CountryService.getSimpleList())//.then(c => this.countries = c)
        data.push(PoleTypePoleService.getSimpleList())//.then(c => this.pole_types = c)
        
        Promise.all(data).then(d => {
            // console.log(d)
            this.parents = d[0]
            this.countries = d[1]
            this.pole_types = d[2]
            this.validateInitInfo()
        })        

        if(this.mode != 'create') {   
            this.initForm() 
        }
    },           
    methods:{
        validateInitInfo() {

            // Si combos vacios redirigir a tablas que hay que llenar 
            if(this.countries.length === 0 || this.pole_types.length === 0) {
                this.showForm = false
                this.info_message += (this.countries.length == 0) ? ' Paises <br>' : '',
                this.info_message += (this.pole_types.length == 0) ? ' Tipos de Polos <br>' : ''
            }
        },
        initForm() {
            // For upd action      
            PoleService.getPole(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.order = e.order
                    this.formData.alias = e.alias
                    this.formData.keyword = e.keyword
                    this.formData.codsv = e.codsv
                    this.formData.longitude = e.longitude
                    this.formData.latitude = e.latitude
                    this.formData.active = e.active
                    this.formData.parent = e.parent_id
                    this.formData.country = e.country_id
                    this.formData.images = e.images
                    console.log(e.pole_types)
                    for(let s of e.pole_types) {
                        this.formData.pole_types_id.push({
                                text: s.alias,
                                value: s.id
                            })
                    }
                    for(let s of e.pole_types) {
                        this.formData.pole_types.push({
                                text: s.alias,
                                value: s.id
                            })
                    }
                }
            })          
        },               
        onPoleTypesSelect(items, litem){
            this.formData.pole_types = items
        },
        onFileSelected(event){
            this.selectedFile = this.$refs.file.files[0]
            // console.log(this.selectedFile)
        },
        onUpload(){
            const form_img = new FormData();
            form_img.append('image',this.selectedFile,this.selectedFile.name)
            this.formData.images = form_img
            //FileUploadService.upload(formdata)
            // console.log(this.formData.images)
        },       
        submitForm(e) {
            e.preventDefault();            
            //console.log(this.formData) 
            if(this.validate(this.formData.order) || this.validate(this.formData.alias) || this.validate(this.formData.active) ||
            this.validate(this.formData.parent) || this.validate(this.formData.country)) { 
                                
                this.formData.pole_types.map(e => {
                    if (typeof e == 'object')                     
                        this.formData.pole_types_id.push(e.value)
                    else 
                        this.formData.pole_types_id.push(e)
                })
                this.formData.parent_id = (typeof this.formData.parent == 'object') ? this.formData.parent.value : this.formData.parent
                this.formData.country_id = (typeof this.formData.country == 'object') ? this.formData.country.value : this.formData.country
                
                //! upload image
                // if(this.selectedFile)
                //     this.onUpload()
                    
                const fd = new FormData()

                // agrgando imagen
                if(this.selectedFile)
                    fd.append('images',this.selectedFile,this.selectedFile.name)

                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }
                // console.log(this.formData.pole_types_id)
                this.load = true
                PoleService.addUpdPole({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        }, 
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    }, 
}
</script>
