<template >
    <div>
        <form action="" class="mt-2" @submit="submitForm">
        <div class="flex my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">            
            <div class="flex flex-row gap-3 w-20 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2">Ancho</label>
                <input type="text" 
                v-model="ancho"
                class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row gap-3 w-20 items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2">Alto</label>
                <input type="text" 
                v-model="alto"
                class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                hsover:bg-green-500 focus:outline-none 
                focus:border-green-700 focus:ring focus:ring-green-200
                active:bg-green-600 
                disabled:opacity-25 transition">Save</button>            
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                hover:text-white 
                focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                active:text-gray-800 active:bg-red 
                disabled:opacity-25 transition">Cancel</button>          
            <div v-if="error_message" class="error-message">
                <i class="fa fa-times-circle"></i>
                <h5 class="text-xl self-center px-2">
                    Error enviando informacion. No se pudo crear el nuevo filtro
                    <p>{{error_message_text}}</p>
                </h5>
                </div>
            <div v-if="success_message" class="success-message">
                <i class="fa fa-check-circle"></i>
                <h5 class="text-xl self-center px-2">La informacin ha sido enviada satisfactoriamente.</h5>
            </div>
        </div> 

        </form>
    </div>
</template>
<script>
import GalleryFilterService from '@/services/gallery/gallery_filter.service'

export default {
    name: "CrudGalleryFilter",
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            info_message: '',
            error_message_text: '',
            error_message: false,
            success_message: false,
            keyword: '',
            ancho: 0,
            alto: 0
        }
    },
    created() {
        this.initForm()
    },
    methods: {
        initForm() {
            GalleryFilterService.getGallery(this.info).then(e => {
                if(this.mode != 'create') {
                    this.ancho = e.width
                    this.alto = e.height
                }
            })
        },
        submitForm(e) {
            e.preventDefault();   

            if(this.alto > 0 && this.ancho > 0) {
                this.keyword = `thumbnail_w${this.ancho}xh${this.alto}`

                const formdata = {
                    filter_keyword: this.keyword,
                    width: this.ancho,
                    height: this.alto
                }
                if (this.info)
                    formdata.id = this.info

                GalleryFilterService.addUpdGallery(formdata, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000);                        
                    }  
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Los valores del filtro deben ser mayores a 0'
                setTimeout(() => {
                    this.error_message = false
                }, 3000);    
            }
        }
    }
}
</script>
