import Authorization from "../user/auth.service";

const ProductTypeService = {
    getList() {
        try {
            return Authorization.checkLogin(`/texts`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            keyword: item.keyword,
                            description: item.description,
                            /* order: item.order,
                            combination_discount: item.combination_discount,
                            travel_agent_margin: item.travel_agent_discount,
                            book_more_than_one_discount: item.book_more_than_one_discount,
                            name_class: item.name_class,
                            id_pyth: item.id_pyth,
                            release: item.release,
                            margin: item.margin,
                            show_in_newsletter: item.show_in_newsletter,
                            pax_info: item.pax_info,
                            db_table_name: item.db_table_name,
                            accept_on_request: item.accept_on_request,
                            accept_payment: item.accept_payment, */
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },        
    async getText(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/text/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdText(producttype_data, is_new = true) {
        try {
            //console.log(producttype_data)
            producttype_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/text/create', producttype_data, 'post'):
                await Authorization.checkLogin(`/text/upd/${producttype_data.id}`, producttype_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/texts/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default ProductTypeService