<template>
  <div>
    <form action="" class="mt-2" @submit="submitForm">
      <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
        <h2 class="text-xl font-bold mb-4 text-gray-800 uppercase">
          {{ mode === 'create' ? 'Registro de Cliente' : 'Editar Cliente' }}
        </h2>

        <!-- Mensajes de xito y error -->
        <div v-if="success_message" class="success-message my-3">
          <i class="fa fa-check-circle"></i>
          <h5 class="text-lg self-center px-2">{{ success_message_text }}</h5>
        </div>
        
        <div v-if="error_message" class="error-message my-3">
          <i class="fa fa-times-circle"></i>
          <h5 class="text-lg self-center px-2">{{ error_message_text }}</h5>
        </div>

        <!-- Campos del formulario -->
        <div class="space-y-4">
          <!-- Nombre y Apellido en misma fila -->
          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Nombre *</label>
            <input
              id="first_name"
              v-model="formData.first_name"
              type="text"
              required
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="Ingrese el nombre"
              disabled
            />
          </div>

          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Apellido *</label>
            <input
              id="last_name"
              v-model="formData.last_name"
              type="text"
              required
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="Ingrese el apellido"
              disabled
            />
          </div>

          <!-- Email -->
          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Email *</label>
            <input
              id="email"
              v-model="formData.email"
              type="email"
              required
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="correo@ejemplo.com"
              disabled
            />
          </div>

          <!-- Direccin -->
          <div class="flex flex-row items-start my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 mt-2">Direccin</label>
            <textarea
              id="address"
              v-model="formData.address"
              rows="3"
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="Ingrese la direccin completa"
              disabled
            ></textarea>
          </div>

          <!-- Telfono y Cdigo Postal en misma fila -->
          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Telfono</label>
            <input
              id="phone"
              v-model="formData.phone"
              type="tel"
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="Ingrese el nmero de telfono"
              disabled
            />
          </div>

          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Cdigo Postal</label>
            <input
              id="zipcode"
              v-model="formData.zipcode"
              type="number"
              min="0"
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              placeholder="Ej: 28001"
              disabled
            />
          </div>

          <!-- Contraseas -->
          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">
              {{ mode === 'create' ? 'Contrasea *' : 'Nueva Contrasea' }}
            </label>
            <input
              id="password"
              v-model="formData.password"
              type="password"
              :required="mode === 'create'"
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              :placeholder="mode === 'create' ? 'Ingrese la contrasea' : 'Dejar vaco para mantener la actual'"
              disabled
            />
          </div>

          <div class="flex flex-row items-center my-3 mx-2">
            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">
              {{ mode === 'create' ? 'Confirmar Contrasea *' : 'Confirmar Nueva Contrasea' }}
            </label>
            <input
              id="repassword"
              v-model="formData.repassword"
              type="password"
              :required="mode === 'create'"
              class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              :placeholder="mode === 'create' ? 'Confirme la contrasea' : 'Confirme la nueva contrasea'"
              disabled
            />
          </div>

          <!-- Nota sobre la contrasea en modo edicin -->
          <div v-if="mode === 'edit'" class="flex flex-row items-center my-3 mx-2">
            <div class="ml-32 w-full">
              <div class="text-sm text-gray-600 bg-blue-50 p-3 rounded border border-blue-200">
                <i class="fas fa-info-circle mr-2"></i>
                <strong>Nota:</strong> Solo complete los campos de contrasea si desea cambiarla. De lo contrario, djelos vacos para mantener la contrasea actual.
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Botones de accin -->
      <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
        <button 
          type="submit" 
          class="inline-flex items-center border border-transparent bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
            hover:bg-green-500 focus:outline-none 
            focus:border-green-700 focus:ring focus:ring-green-200
            active:bg-green-600 
            disabled:opacity-25 transition" 
          :disabled="load"
        >
          {{ mode === 'create' ? 'Crear Cliente' : 'Actualizar Cliente' }}
          <span v-if="load" class="flex items-center ml-2">
            <i class="fa fa-spinner fa-pulse"></i>
          </span>
        </button>
        <button 
          type="button" 
          class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2            
            focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
            active:bg-red-50
            disabled:opacity-25 transition" 
          :disabled="load" 
          @click="cancelForm"
        >
          Cancelar
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import MarketplaceDeliveryService from "@/services/marketplace/marketplace_client.service";
import { EncryptionService } from "@/services/marketplace/marketplace_encryption.service";
import Swal from 'sweetalert2';

export default {
  components: {},
  props: {
    info: {},
    mode: {
      default: 'create'
    }
  },
  name: "CrudMarketplaceClient",
  data() {
    return {
      load: false,
      error_message: false,
      error_message_text: '',
      success_message: false,
      success_message_text: '',

      formData: {
        id: null,
        first_name: '',
        last_name: '',
        email: '',
        address: '',
        phone: '',
        zipcode: '',
        password: '',
        repassword: '',
      }
    }
  },
  created() {
    this.initForm();
  },
  watch: {
    mode() {
      this.initForm();
    }
  },
  methods: {
    initForm() {            
      this.load = true;
      
      if (this.mode === 'create') {
        // Inicializar formulario vaco para crear              
        this.formData = {                    
          id: null,
          first_name: '',
          last_name: '',
          email: '',
          address: '',
          phone: '',
          zipcode: '',
          password: '',
          repassword: '',
        };
        this.load = false;
      } else {
        // Cargar datos existentes para editar
        MarketplaceDeliveryService.getMarketplaceClient(this.info)
          .then(item => {   
            this.formData = {
              id: item.id || null,
              first_name: item.first_name || '',
              last_name: item.last_name || '',
              email: item.email || '',
              address: item.address || '',
              phone: item.phone || '',
              zipcode: item.zipcode || '',
              password: '',
              repassword: '',
            };
          })
          .catch(error => {
            console.error('Error loading delivery config:', error);
            this.error_message = true;
            this.error_message_text = 'Error al cargar la configuracin de entrega';
          }).finally(() => { 
            this.load = false;
          });
      }
    },

    async submitForm(e) {            
      e.preventDefault();
      this.error_message = false;
      this.success_message = false;
      
      // Validaciones bsicas
      if (this.mode === 'create' && !this.formData.password) {
        this.error_message = true;
        this.error_message_text = 'La contrasea es requerida para crear un usuario';
        return;
      }
      
      if (this.formData.password && this.formData.password !== this.formData.repassword) {
        this.error_message = true;
        this.error_message_text = 'Las contraseas no coinciden';
        return;
      }
      
      if (this.formData.password && this.formData.password.length < 6) {
        this.error_message = true;
        this.error_message_text = 'La contrasea debe tener al menos 6 caracteres';
        return;
      }

      if (this.formData.zipcode && !/^\d+$/.test(this.formData.zipcode.toString())) {
        this.error_message = true;
        this.error_message_text = 'El cdigo postal debe contener solo nmeros';
        return;
      }      
      
      this.load = true;
      
      const submitData = {        
        first_name: this.formData.first_name,
        last_name: this.formData.last_name,
        email: this.formData.email,
        address: this.formData.address,
        phone: this.formData.phone,
        zipcode: this.formData.zipcode || null,
      };

      if (this.mode !== 'create') submitData.id = this.formData.id

      const publicKey = sessionStorage.getItem("publicKey")

      if (this.formData.password && publicKey) {        
        submitData.password = await EncryptionService.encryptForBackend(this.formData.password,publicKey);        
      }
            
      const submitPromise = this.mode === 'create' 
        ? MarketplaceDeliveryService.addUpdMarketplaceClient(submitData)
        : MarketplaceDeliveryService.addUpdMarketplaceClient(submitData, false);
      
      submitPromise
        .then(response => {
          this.success_message = true;
          this.success_message_text = this.mode === 'create' 
            ? 'Cliente creado correctamente' 
            : 'Cliente actualizado correctamente';
          
          // Mostrar SweetAlert de xito
          Swal.fire({
            icon: 'success',
            title: 'xito!',
            text: this.success_message_text,
            timer: 2000,
            showConfirmButton: false
          });

          setTimeout(() => {
            this.success_message = false;
            this.$emit('close');
          }, 2000);
        })
        .catch(error => {
          this.error_message = true;
          this.error_message_text = this.mode === 'create' 
            ? 'Error al crear el cliente' 
            : 'Error al actualizar el cliente';
          console.error('Error submitting form:', error);

          // Mostrar SweetAlert de error
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: this.error_message_text,
            timer: 3000,
            showConfirmButton: false
          });

          setTimeout(() => {
            this.error_message = false;
          }, 3000);
        })
        .finally(() => {
          this.load = false;
        });
    },        
    
    cancelForm() {
      Swal.fire({
        title: 'Ests seguro?',
        text: 'Los cambios no guardados se perdern',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'S, cancelar',
        cancelButtonText: 'No, continuar editando'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$emit('close');
        }
      });
    },
  }
}
</script>

<style scoped>
</style>