import Authorization from "../user/auth.service";

const base_url = 'places'
const PlaceService = {
    getList() {
        try {
            return Authorization.checkLogin(`/${base_url}`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,                            
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/${base_url}`).then(res => {
                if (res.status == 200) {
                    const countries = []
                    for (let item of res.data) {
                        countries.push({
                            value: item.id,
                            text: item.alias
                        })
                    }
                    return countries
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    async getPlace(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {            
            console.log(e.response)            
        }
    },
    async addUpdPlace(place_data, is_new = true) {        
        try {
            place_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin(`/${base_url}/create`, place_data, 'post')
            : await Authorization.checkLogin(`/${base_url}/upd/${place_data.id}`, place_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default PlaceService