<template >
    <div>
        <form action="" class="mt-2"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">active</label>
                <input type="checkbox" v-model="formData.active" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Code</label>
                <input v-model="formData.code" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Name</label>
                <input v-model="formData.name" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>                          
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">ISO Code</label>
                <input v-model="formData.iso_code" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>                          
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Symbol</label>
                <input v-model="formData.symbol" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>                          
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Rate</label>
                <input v-model="formData.rate" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>                          
            
        </div>
            
        <!-- Button -->        
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                hsover:bg-green-500 focus:outline-none 
                focus:border-green-700 focus:ring focus:ring-green-200
                active:bg-green-600 
                disabled:opacity-25 transition"
                :disabled="load">
                Salvar&nbsp;
                <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button> 
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                hover:text-white 
                focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                active:text-gray-800 active:bg-red 
                disabled:opacity-25 transition"
                :disabled="load"
                >Cancelar</button> 
            <div v-if="error_message" class="error-message">
                <i class="fa fa-times-circle"></i>
                <h5 class="text-xl self-center px-2">
                    Error enviando informacion. No se pudo crear el nuevo usuario
                    <p>{{error_message_text}}</p>
                </h5>
                </div>
            <div v-if="success_message" class="success-message">
                <i class="fa fa-check-circle"></i>
                <h5 class="text-xl self-center px-2">La informacin ha sido enviada satisfactoriamente.</h5>
            </div>
        </div>

        </form>
    </div>
</template>
<script>
import CurrencyService from "@/services/common/currency.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudCurrency",
    components: {        
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,   
            load: false,         
            formData: {
                //general info
                id: '',
                name: '',
                code: '',
                iso_code: '',
                symbol: '',
                rate: '',
                active: '',                
            },
        }
    },
    created() {    
        this.initForm()    
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            CurrencyService.getCurrency(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.code = e.code
                    this.formData.iso_code = e.iso_code
                    this.formData.name = e.name
                    this.formData.symbol = e.symbol
                    this.formData.rate = e.rate
                    this.formData.active = e.active
                }
            })
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            //console.log(this.formData)
            e.preventDefault();            
            if(this.validate(this.formData.code) || this.validate(this.formData.name) || this.validate(this.formData.rate)) {                
                this.load = true;
                CurrencyService.addUpdCurrency(this.formData, (this.mode == 'create')).then(res => {
                    this.load = false;
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        async getCurrency(e) {
            console.log(e)
        },
    }    
}
</script>
