<template >
    <div>
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores: <br><br><b v-html="info_message"></b></span>
        </div>
        <form id="carPickupPlaceForm" action="" class="mt-2"
         @submit="submitForm">             
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Activo</label>
                    <input v-model="formData.active" required type="checkbox" class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre</label>
                    <input v-model="formData.alias" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Direccin</label>
                    <input v-model="formData.address" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Compaa</label>
                    <model-select :options="companies"
                            v-model="formData.company_id"
                            placeholder="Seleccione una compaa *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Tipo de Lugar</label>
                    <model-select :options="places"
                            v-model="formData.place_type_id"
                            placeholder="Seleccione tipo de lugar *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
                <div class="flex gap-6 w-full">
                    <div class="flex flex-row gap-1 w-1/4 items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Hora de recogida</label>
                        <select name="pickup_time" v-model="formData.pickup_time"
                        class="border mt-1 block w-2/6 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                            <option v-for="(item, index) in hours" :key="index" :value="item" >{{ item }}</option>
                        </select> 
                        <div class="flex flex-col gap-1 px-2">
                            <label class="text-sm font-medium text-gray-700 ">
                                AM
                                <input type="radio" name="pickup_time" value="AM" v-model="pickup">
                            </label>                        
                            <label class="text-sm font-medium text-gray-700 ">
                                PM
                                <input type="radio" name="pickup_time" value="PM" v-model="pickup">
                            </label>                        
                        </div>
                    </div>  

                    <div class="flex flex-row gap-1 w-1/4 items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Hora de recogida</label>
                        <select name="dropoff_time" v-model="formData.dropoff_time"
                        class="border mt-1 block w-2/6 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                            <option v-for="(item, index) in hours" :key="index" :value="item" >{{ item }}</option>
                        </select> 
                        <div class="flex flex-col gap-1 px-2">
                            <label class="text-sm font-medium text-gray-700 ">
                                AM
                                <input type="radio" name="dropoff_time" value="AM" v-model="dropoff">
                            </label>                        
                            <label class="text-sm font-medium text-gray-700 ">
                                PM
                                <input type="radio" name="dropoff_time" value="PM" v-model="dropoff">
                            </label>                        
                        </div>
                    </div> 
                </div>
            </div>      

            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">                
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>  
            </div>    
        </form>        
    </div>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import PickupService from "@/services/car/pickup_places.service";
import CarCompanyServices from "@/services/car/car_company.service";
import PlaceServices from "@/services/common/place.service";

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudCarPickupPlaces",    
    components: {        
        ModelSelect,                
    },
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la habitacin correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true,           
        pickup: 'AM',     
        dropoff: 'PM',   
        companies: [],  
        places: [],  
        formData: {},        
    }),
    created() { 
        const data = []
        data.push(CarCompanyServices.getSimpleList())
        data.push(PlaceServices.getSimpleList())
        Promise.all(data).then(d => {
            // console.log(d)
            this.companies = d[0]
            this.places = d[1]
            this.validateInitInfo()            
        })  
        if(this.mode != 'create') {   
            this.initForm() 
        }
    },    
    computed: {
        hours() {
            const hours = []
            for(let i=1; i<=12; i++){
                hours.push(i + ':00')
            }
            return hours
        }
    },
    methods:{     
        validateInitInfo() {
            if(this.companies.length == 0) {
                this.showForm = false
                this.info_message = (this.companies.length == 0) ? 'Compaas <br>'  : ''
            }
        },   
        initForm() {
            PickupService.getPickupPlace(this.info).then(e => {
                this.clearForm()

                if(this.mode != 'create') {                    
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.address = e.address
                    this.formData.company_id = e.car_company.id
                    this.formData.place_type_id = e.places_type.id                    
                    this.formData.pickup_time = this.parseTime(e.pickup_time).time
                    this.formData.dropoff_time = this.parseTime(e.dropoff_time).time
                    this.formData.active = e.active
                    this.pickup = this.parseTime(e.pickup_time).part_day
                    this.dropoff = this.parseTime(e.dropoff_time).part_day
                }
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias) && this.validate(this.formData.address)) {   
                this.load = true

                const data = {                    
                    alias: this.formData.alias,
                    address: this.formData.address,    
                    car_company_id: (typeof this.formData.company_id == 'object') ? this.formData.company_id.value : this.formData.company_id,            
                    place_type_id: (typeof this.formData.place_type_id == 'object') ? this.formData.place_type_id.value : this.formData.place_type_id,            
                    pickup_time: this.formData.pickup_time + ' ' + this.pickup,
                    dropoff_time: this.formData.dropoff_time + ' ' + this.dropoff,                
                    active: 1,
                }
                if('id' in this.formData) data.id = this.formData.id

                PickupService.addUpdPickupPlace(data, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(this.mode == 'create')
                        this.clearForm()
                    if(!res) {                         
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el lugar'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);                                                
                    } else {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false                            
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }            
        },        
        clearForm() {
            this.formData = {                
                alias: '',
                address: '', 
                company_id: {
                    value: 0,
                    text: ''
                },
                place_type_id: {
                    value: 0,
                    text: ''
                },               
                pickup_time: '9:00',
                dropoff_time: '5:00',                
                active: 1,
            }
        },
        parseTime(time) {
            const parse = time.split(' ')
            return {
                time: parse[0],
                part_day: parse[1],
            }
        }
    },
}
</script>