<template >
    <div>
        <form action="" class="mt-2"
        @submit="submitForm">
        <div class="flex my-3 px-3 gap-3 border-double border border-gray-400 rounded-lg">                        
            <!-- Columna de datos variables -->
            <div class="flex flex-col gap-2 my-3 mx-2 w-50">
                <div class="flex items-center gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Categora de Imagen</label>
                    <model-select :options="classes"
                            v-model="className"
                            placeholder="Seleccione Clase *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>                   
                </div>                
                <div class="flex items-center gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Subcategoria</label>
                    <input type="text" 
                    v-model="formData.subcategory"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex items-center gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Producto <span class="hidden" ref="product_load"><i class="fa fa-spinner fa-pulse"></i></span></label>
                    <model-select :options="obj_list"
                            v-model="formData.id_object_class_list"
                            placeholder="Seleccione un elemento"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>                   
                </div>
                <!-- <div class="flex items-center gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Filtros</label>
                    <multi-select :options="filters"
                        :selected-options="formData.filters"
                        placeholder="Seleccione los filtros"       
                        @select="onFilterSelect"                                 
                        required   
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </multi-select>                       
                </div> -->
            </div>                                    
            <!-- Columna de datos fijos -->
            <div class="flex flex-col gap-2 my-3 mx-2 w-50">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-100">nombre de imagen Original: <b>{{ formData.image_keyword }}</b> </label>
                <label v-if="(mode != 'create')" class="uppercase block text-sm font-medium text-gray-700 mx-2 w-100">Nombre Imagen guardado: <b>{{ formData.image_name }}</b> </label>
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Tipo de fichero: <b>{{ formData.ext }}</b> </label>
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Ao: <b>{{ formData.anno }}</b> </label>
            </div>

            <!-- Imagen Original -->
            <div class="flex flex-col gap-3 my-3 mx-2 w-50">                
                <label class="uppercase block text-sm font-medium text-gray-700">Fichero Imagen </label>
                <div>
                    <input type="file" ref="file" @change="imageSelected" accept="image/*" class="">                    
                </div>
                <figure class="relative w-50 rounded-lg" v-if="image_loaded">
                    <img :src="image_url" class="rounded-lg">                    
                    <span class="absolute image-delete-pos text-red cursor-pointer text-2xl" @click="clearImage"><i class="fas fa-times-circle"></i></span>
                </figure>                
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
            <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                :disabled="load">
                    Salvar&nbsp;
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
            </button>           
            <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition"
                :disabled="load">
                Cancelar</button>  
            <div v-if="error_message" class="error-message">
                <i class="fa fa-times-circle"></i>
                <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                <i class="fa fa-check-circle"></i>
                <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
            </div>          
        </div> 

        </form>
    </div>
</template>
<script>
import Authorization from "@/services/user/auth.service";
import Services from "@/services/index";

import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import moment from 'moment'

export default {
    name: "CrudGallery",
    props: {
        info: 0,
        mode: {
            default: 'create'
        }
    },
    components: {
        ModelSelect,
    },
    data: () => ({  
        success_message_text: 'Se salvaron los datos correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,      
        image_loaded: false,  
        load: false,   
        image_url: '',
        classes: [
            {text: 'Hotel', value: 'hotel'},
            {text: 'Polo', value: 'pole'},
        ],
        className: '',
        obj_list: [],
        formData: {
            id: 0,
            class_name: '',
            id_object_class_list: {
                value: '',
                text: ''
            },
            id_object_class: 0,
            subcategory: '',
            lang: 'all',
            image_name: '',
            image_keyword: '',
            ext: '',
            anno: moment().format('YYYY'),
            order: 0,
            active: 1,
        },                
    }),
    created() {         
        this.initForm()
    }, 
    watch: {
        className(item) {
            this.formData.class_name = item
            this.updObjList()
        }
    },      
    methods: {
        initForm() {
            if (this.mode != 'create') {
                Services.GalleryService.getImage(this.info).then(async e => {
                    this.formData.id = e.id,
                    this.formData.image_name = e.class_name+'_'+e.id,
                    this.className = e.class_name,
                    this.formData.class_name = e.class_name,
                    this.formData.id_object_class_list = e.id_object_class,
                    this.formData.id_object_class = e.id_object_class,
                    this.formData.subcategory = e.subcategory,
                    this.formData.lang = e.lang,
                    this.formData.image_keyword = e.image_keyword,
                    this.formData.ext = e.ext,
                    this.formData.anno = e.anno,
                    this.formData.order = e.order
                    if(e.resources) {                    
                        for(let item of e.resources) {
                            if(item.image == 'original'){
                                this.image_url = item.url
                                this.image_loaded = true
                            }
                        }
                    }   
                    if(e.thumbs) {
                        for(let t of e.thumbs) {
                            this.formData.filters.push({
                                text: t.gallery_filter.filter_keyword,
                                value: t.gallery_filter.id
                            })
                        }
                    } 
                })
            }
        },        
        imageSelected(e) {            
            const file = e.target.files[0];
            this.image_url = URL.createObjectURL(file);
            this.image_loaded = true

            console.log(file)
            this.formData.image_keyword = file.name.split('.')[0]
            this.formData.ext = file.name.split('.')[1]
        }, 
        clearImage(){
            this.image_url = ''
            this.formData.image_keyword = ''
            this.formData.ext = ''
            this.image_loaded = false
            this.$refs.file.value = ''
        },   
        async updObjList() {
            this.$refs.product_load.classList.remove("hidden");
            try {
                if(this.formData.class_name == 'hotel') {                
                    this.obj_list = await Services.HotelService.getSimpleList()
                } else {
                    this.obj_list = await Services.PoleService.getSimpleList()
                }
                this.$refs.product_load.classList.add("hidden");
            } catch(e) {
                console.log(e)
            }
            return this.obj_list
        },                
        submitForm(e) {
            e.preventDefault();           

            if(this.validate(this.formData.class_name) && this.image_url != ''){   
                this.formData.id_object_class = (typeof this.formData.id_object_class_list == 'object') ? this.formData.id_object_class_list.value : this.formData.id_object_class_list                
                
                this.load = true
                const fd = new FormData()

                // agrgando imagen
                const selectedFile = this.$refs.file.files[0]
                if(selectedFile)
                    fd.append('images', selectedFile, selectedFile.name)

                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }
                // console.log(fd)
                Services.GalleryService.addUpdGallery({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true                    
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }
                })
                
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
    }
}
</script>
<style scoped>
    .image-delete-pos{
        top: 140px;
        right: -17px;
    }
</style>