<template >
    <div>
        <form action="" class="mt-2"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                                              
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Show in newsletter</label>
                <input v-model="formData.show_in_newsletter" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Pax info</label>
                <input v-model="formData.pax_info" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Activo</label>
                <input v-model="formData.active" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Acepta Solicitudes</label>
                <input v-model="formData.accept_on_request" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Acepta pago</label>
                <input v-model="formData.accept_payment" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Alias</label>
                <input v-model="formData.alias" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Orden</label>
                <input v-model="formData.order" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Combinacin de Descuento</label>
                <input v-model="formData.combination_discount" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Descuento de Agente</label>
                <input v-model="formData.travel_agent_discount" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">book more than one discount</label>
                <input v-model="formData.book_more_than_one_discount" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre clase</label>
                <input v-model="formData.name_class" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">id pyth</label>
                <input v-model="formData.id_pyth" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Liberado</label>
                <input v-model="formData.release" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Margen</label>
                <input v-model="formData.margin" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">DB Tabla Name</label>
                <input v-model="formData.db_table_name" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
            </div>
            
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition">Save</button>            
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition">Cancel</button> 
                <div v-if="error_message" class="flex bg-red-500 rounded-3xl w-full text-left text-4xl p-4 my-3">

                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="flex bg-green-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div>

        </form>
    </div>
</template>
<script>
import ProductTypeService from "@/services/common/product_type.service"; 

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudProductType",
    components: {        
    },
    data() {
        return {
            error_message_text: '',
            error_message: false,
            success_message: false,
            formData: {
                //general info
                id: '',
                order: '',
                alias: '',
                combination_discount: '',
                travel_agent_discount: '',
                book_more_than_one_discount: '',
                name_class: '',
                id_pyth: '',
                release: '',
                margin: '',
                show_in_newsletter: '',
                pax_info: '',
                active: '',
                db_table_name: '',
                accept_on_request: '',
                accept_payment: '',

            },
        }
    },
    created() {   
        this.initForm()     
    },
    watch: {
        mode() {
            //console.log(this.info)
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            ProductTypeService.getProductType(this.info).then(e => {
                if(this.mode != 'create') {                
                    this.formData.id = e.id
                    this.formData.order = e.order
                    this.formData.alias = e.alias
                    this.formData.combination_discount = e.combination_discount
                    this.formData.travel_agent_discount = e.travel_agent_discount
                    this.formData.book_more_than_one_discount = e.book_more_than_one_discount
                    this.formData.name_class = e.name_class
                    this.formData.id_pyth = e.id_pyth
                    this.formData.release = e.release
                    this.formData.margin = e.margin
                    this.formData.show_in_newsletter = e.show_in_newsletter
                    this.formData.pax_info = e.pax_info
                    this.formData.active = e.active
                    this.formData.db_table_name = e.db_table_name
                    this.formData.accept_on_request = e.accept_on_request
                    this.formData.accept_payment = e.accept_payment
                }
            })
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            
            e.preventDefault();            
            if(
                this.validate(this.formData.order) || 
                this.validate(this.formData.alias) || 
                this.validate(this.formData.combination_discount) ||
                this.validate(this.formData.travel_agent_discount) || 
                this.validate(this.formData.book_more_than_one_discount)) {                   
                
                ProductTypeService.addUpdProductType(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = false
                this.success_message = true
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getProductType(e) {
            console.log(e)
        },
    }
}
</script>
