<template >
    <div>
        <form action="" class="mt-2"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">            
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Activo</label>
                <input type="checkbox" class="p-2" v-model="formData.active">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Habitacin Luxury</label>
                <input type="checkbox" class="p-2" v-model="formData.is_luxury_room">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Precio por habitacin</label>
                <input type="checkbox" class="p-2" v-model="formData.apply_price_per_room">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Hotel</label>
                <model-select :options="hotels"
                        v-model="formData.hotel_list"
                        placeholder="Seleccione el hotel *"
                        required
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                  
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Tipo de habitacin</label>
                <model-select :options="room_types"
                        v-model="formData.room_type_list"
                        placeholder="Seleccione el tipo de habitacin *"
                        required
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Plan alimenticio</label>
                <model-select :options="hotel_categories"
                        v-model="formData.room_plan_list"
                        placeholder="Seleccione el plan de la Habitacin *"
                        required
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select>                    
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Cdigo</label>
                <input type="text" 
                v-model="formData.code"
                class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <!-- <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Cantidad de personas</label>
                <input type="text" 
                v-model="formData.quantity"
                class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div> -->
            <div class="flex gap-3">
                <div class="flex items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Mximo de personas</label>
                    <input type="text" 
                    v-model="formData.max_pax_quantity"
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <!-- <vue-repeater v-model="formData.combinations"></vue-repeater> -->                
                <!-- <div class="flex items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Mximo de adultos</label>
                    <input type="text" 
                    v-model="formData.max_adults_quantity"
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Mximo de nios</label>
                    <input type="text" 
                    v-model="formData.max_childrens_quantity"
                    class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div> -->
            </div>
            <!-- Combinaciones -->
            <div class="flex gap-3">
                <div class="flex flex-col items-center gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Combinaciones</label>
                    <button type="button" class="border border-transparent bg-orange rounded-md font-semibold text-md text-white uppercase tracking-widest px-2
                    hover:border-lightGray" @click="AddCombination">
                        <i class="fas fa-plus"></i>
                    </button>
                </div>
                <no-pax-room :combinations="formData.combinations" v-on:deleteComb="DelCombination"></no-pax-room>
            </div>
            
            <div class="flex items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Orden</label>
                <input type="text" 
                v-model="formData.order"
                class="flex border mt-1 w-20 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>            
            
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load">
                    Salvar&nbsp;
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                    </button>            
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar</button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div> 

        </form>
    </div>
</template>
<script>
import HotelService from "@/services/hotel/hotel.service"
import HotelRoomTypeService from "@/services/hotel/hotel_room_type.service"
import HotelCategoryService from "@/services/hotel/hotel_category.service"
//import HotelRoomTypeCategoryService from "@/services/hotel/hotel_room_type_category.service"
import HotelRoomService from "@/services/hotel/hotel_room.service"

import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import NoPaxRoom from '@/components/NoPaxRoom'

export default {
    name: "CrudHotelRoom",
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    components: {
        ModelSelect,
        NoPaxRoom
    },
    data: () => ({  
        success_message_text: 'Se salvaron los datos de la habitacin correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,        
        load: false,        
        hotels: [],
        room_types: [],
        hotel_categories: [],
        formData: {
            id: 0,
            hotel_list: {
                value: '',
                text: ''
            },
            hotel_id: [],
            room_type_list: {
                value: '',
                text: ''
            },
            room_type_id: [],
            room_plan_list: {
                value: '',
                text: ''
            },
            room_plan_id: [],
            code: '',
            quantity: 0,
            max_pax_quantity: 0,
            combinations: [],
            // max_adults_quantity: 0,
            // max_childrens_quantity: 0,
            active: 0,
            is_luxury_room: 0,
            order: 0,
            created_at: '',
            updated_at: '',
            apply_price_per_room: 0,

        },        
    }),
    created() { 
        const data = []
        data.push(HotelRoomTypeService.getSimpleList())//.then(c => this.room_types = c)
        data.push(HotelCategoryService.getSimpleList())//.then(c => this.hotel_categories = c)
        //HotelRoomTypeCategoryService.getSimpleList().then(c => this.room_type_categories = c)
        data.push(HotelService.getSimpleList())//.then(c => this.hotels = c)
        Promise.all(data).then(d => {
            // console.log(d)
            this.room_types = d[0]
            this.hotel_categories = d[1]
            this.hotels = d[2]
            if(this.mode != 'create') {             
                this.initForm()
            }
        })           
    },  
     methods:{
        initForm() {
            HotelRoomService.getHotelRoom(this.info).then(e => {
                this.formData.id = e.id
                this.formData.hotel_id = e.hotel_id
                this.formData.hotel_list = e.hotel_id
                this.formData.room_type_id = e.room_type_id
                this.formData.room_type_list = e.room_type_id
                this.formData.room_plan_id = e.room_plan_id
                this.formData.room_plan_list = parseInt(e.room_plan_id)                
                this.formData.code = e.code
                this.formData.quantity = e.quantity
                this.formData.max_pax_quantity = e.max_pax_quantity
                // this.formData.max_adults_quantity = e.max_adults_quantity
                // this.formData.max_childrens_quantity = e.max_childrens_quantity
                this.formData.active = e.active
                this.formData.is_luxury_room = e.is_luxury_room
                this.formData.order = e.order
                this.formData.created_at = e.created_at
                this.formData.updated_at = e.updated_at
                this.formData.apply_price_per_room = e.apply_price_per_room                                  
                this.formData.combinations = e.hotel_room_pax_combinations
            })
        },
        submitForm(e) {
            e.preventDefault();           

            if(this.validate(this.formData.hotel_list.value) || this.validate(this.formData.room_type_list.value) || this.validate(this.formData.quantity) ||
            this.validate(this.formData.max_adults_quantity) || this.validate(this.formData.max_childrens_quantity) || this.validate(this.formData.max_pax_quantity) ||
            this.validate(this.formData.order)){
                               
                this.formData.hotel_id = (typeof this.formData.hotel_list == 'object') ? this.formData.hotel_list.value : this.formData.hotel_list
                this.formData.room_type_id = (typeof this.formData.room_type_list == 'object') ? this.formData.room_type_list.value : this.formData.room_type_list
                this.formData.room_plan_id = (typeof this.formData.room_plan_list == 'object') ? this.formData.room_plan_list.value : this.formData.room_plan_list
                this.formData.active = (this.formData.active == '') ? 0 : 1
                this.formData.is_luxury_room = (this.formData.is_luxury_room == '') ? 0 : 1
                this.formData.apply_price_per_room = (this.formData.apply_price_per_room == '') ? 0 : 1
                
                this.load = true
                HotelRoomService.addUpdHotelRoom(this.formData, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true                    
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }
                })
                
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        AddCombination() {
            this.formData.combinations.push({
                max_adults_quantity: 0,
                max_children_quantity: 0,
                max_infants_quantity: 0
            })
        },
        DelCombination(index) {
            this.formData.combinations.splice(index,1)
        }
     }
}
</script>
