<template>
    <div class="w-3/5 flex flex-col gap-3">
        <div class="flex gap-3 border rounded-xl items-center px-4" v-for="(item, index) in list" :key="index">        
            <div class="flex gap-3 w-11/12">
                <div class="w-3/5 flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Suplemento</label>
                    <select class="border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        :ref="'select'+index"
                        @change="updSuplement(index)">
                        <option value="0">Seleccione el suplemento</option>
                        <option v-for="(s, i) in sup" :key="i" :value="s.value" :selected="(s.value == item.suplement)">{{ s.text }}</option>
                    </select>
                    <!-- <model-select :options="sup"      
                        v-model="item.suplement"              
                        placeholder="Seleccione el suplemento"
                        required 
                        $ref="reference"                       
                        class="border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>                -->
                </div>  
                <div class="flex flex-row items-center my-3 mx-2">            
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Precio</label>
                    <input type="text" required
                        v-model="item.price"
                        @change="updSuplement(index)"
                        class="border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">           
                </div>
            </div>
            <span class="text-2xl w-1/12 text-red text-center cursor-pointer" @click="deleteSuplement(index)" v-if="(list.length > 1)"><i class="fa fa-times"></i></span>
        </div>
    </div>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

export default {
    props: {
        sup: [],
        list: {
            default: [
                {
                    suplement: {
                        value: '',
                        text: ''
                    },
                    price: 0                
                }
            ]
        }
    },
    name: 'Suplement',
    components: {
        ModelSelect
    },        
    methods: {
        updSuplement(index) {
            this.list[index].suplement = this.$refs['select'+index][0].selectedOptions[0].value            
            this.$emit('upd',{index, obj: this.list[index]})
        },
        deleteSuplement(index) {            
            this.$emit('delete', index)
        }
    }    
}
</script>
