<template>
    <div class="flex my-1 w-full p-2 border-double border border-gray-400 rounded-lg">                                              
      <div class="w-50 my-3 mx-2">
        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Ttulo</label>
        <input 
          v-model="title" 
          type="text" 
          @input="updateValue" 
          class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        >
      </div>
  
      <div class="w-50 my-3 mx-2">
        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Valor</label>
        <input 
          v-model="value" 
          type="text" 
          @input="updateValue" 
          class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        >
      </div>            
  
      <button 
        type="button" 
        class="self-start text-red rounded-md font-semibold text-xs"
        @click="$emit('delete', index)"
      >
        <i class="fa fa-trash"></i>
      </button>        
    </div>            
  </template>
  
  <script>
  export default {
    name: "Features",
    props: {
      info: { type: Object, required: true },
      index: { type: Number, required: true },
    },
    data() {
      return {     
        title: this.info.title || '',
        value: this.info.value || '',
      }
    },
    methods: {
      updateValue() {
        this.$emit('update', { index: this.index, title: this.title, value: this.value })
      }
    }
  }
  </script>