<template >
    <div>
        <form action="" class="mt-2"
        @submit="submitForm">
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">            
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">active</label>
                <input v-model="formData.active" type="checkbox" class="p-2" >
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Alias</label>
                <input v-model="formData.alias" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword</label>
                <input v-model="formData.keyword" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Website URL</label>
                <input v-model="formData.website_url" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Order</label>
                <input v-model="formData.order" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load">
                    Salvar&nbsp;
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                    </button>            
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition"
                    :disabled="load">
                    cancelar
                    </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div> 

        </form>
    </div>
</template>
<script>
       
import HotelChainService from "@/services/hotel/hotel_chain.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },     
    name: "CrudHotelChain",
    components: {
        },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,
            formData: {
                //general info
                id: '',
                alias: '',
                keyword: '',
                website_url: '',
                active: '',
                order: '',
            },
        }
    },
    created() {
        if(this.mode != 'create') { 
            this.initForm()
        }
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods:{
        initForm() {
            // For upd action      
            HotelChainService.getHotelChain(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.keyword = e.keyword
                    this.formData.website_url = e.website_url
                    this.formData.active = e.active
                    this.formData.order = e.order
                }
            })          
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            e.preventDefault();            
            if(this.validate(this.formData.alias) || this.validate(this.formData.keyword) || this.validate(this.formData.order) ) {
                this.load = true
                HotelChainService.addUpdHotelChain(this.formData, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }  
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },        
    }   
}
</script>
