<template >
    <div>
        <div class="info-message flex item-center p-4 rounded-xl" v-if="!showForm">
            <i class="fas fa-exclamation-circle"></i>
            <span>Falta informacin en los siguientes nomencladores de carro: <br><br><b v-html="info_message"></b></span>
        </div>
        <form id="carForm" action="" v-if="showForm" class="mt-2" enctype="multipart/form-data"
         @submit="submitForm">             
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Activo</label>
                    <input v-model="formData.active" required type="checkbox" class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre</label>
                    <input v-model="formData.alias" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword</label>
                    <input v-model="formData.keyword" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Elctrico</label>
                    <input v-model="formData.is_electric" required type="checkbox" class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Capacidad en tanque</label>
                    <input v-model="formData.tanque" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Categora</label>
                    <model-select :options="categories"
                            v-model="formData.category_id"
                            placeholder="Seleccione una categora *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Compaa</label>
                    <model-select :options="companies"
                            v-model="formData.company_id"
                            placeholder="Seleccione una compaa *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Tipo de mecnica</label>
                    <model-select :options="engines"
                            v-model="formData.engine_id"
                            placeholder="Seleccione un tipo de mecnica *"
                            required
                            class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </model-select>  
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Capacidad de personas</label>
                    <input v-model="formData.num_pax" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Precio desde</label>
                    <input v-model="formData.price_from" required type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Imagen</label>
                    <add-images class="w-95" :index="1" :info="img" v-on:imgStatus="updImage"></add-images>                    
                </div>                
            </div>      

            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">                
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>  
            </div>    
        </form>        
    </div>
</template>
<script>
import moment from 'moment'
import AddImages from '@/components/AddImages';
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import CarServices from "@/services/car/car.service";
import CarCategoryServices from "@/services/car/car_category.service";
import CarEngineServices from "@/services/car/car_engine.service";
import CarCompanyServices from "@/services/car/car_company.service";

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudCarPickupPlaces",    
    components: {        
        ModelSelect,  
        AddImages              
    },
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la habitacin correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true,           
        categories: [],  
        engines: [],  
        galleries: [],
        companies: [],
        img: {
            id: 0,
            class_name: 'car',            
            id_object_class: 0,
            subcategory: '',
            lang: 'all',
            image_name: '',
            image_keyword: '',
            url: '',
            file: '',
            ext: '',
            anno: moment().format('YYYY'),
            order: 1,
            active: 1,
        },
        formData: {},        
    }),
    created() { 
        let data =[]
        data.push(CarCategoryServices.getSimpleList())//.then(c => this.poles = c)
        data.push(CarEngineServices.getSimpleList())//.then(c => this.categories = c)
        data.push(CarCompanyServices.getSimpleList())//.then(c => this.categories = c)
        
        Promise.all(data).then(d => {
            // console.log(d)
            this.categories = d[0]
            this.engines = d[1]
            this.companies = d[2]
            
            this.validateInitInfo()
        }) 
        this.clearForm()
        
        if(this.mode != 'create') {             
            this.initForm()
        }
    },        
    methods:{     
        initGallery(images) {
            this.galleries = []
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')                                
                this.img.id = img.id
                this.img.class_name = img.class_name
                this.img.id_object_class = img.id_object_class
                this.img.subcategory = img.subcategory
                this.img.lang = img.lang
                this.img.image_name = img.image_keyword
                this.img.image_keyword = img.image_keyword
                this.img.url = original[0].url
                this.img.file = {}
                this.img.ext = img.ext
                this.img.anno = img.anno
                this.img.order = img.order
                this.img.active = img.active
            }
            
        },   
        validateInitInfo() {
            if(this.categories.length == 0 || this.engines.length == 0 || this.companies.length == 0) {
                this.showForm = false
                this.info_message = (this.companies.length == 0) ? 'Compaas <br>'  : ''
                this.info_message = (this.categories.length == 0) ? 'Categorias <br>'  : ''
                this.info_message += (this.engines.length == 0) ? ' Mecnica <br>' : ''
            }
        },
        initForm() {
            CarServices.getCar(this.info).then(e => {
                this.clearForm()

                if(this.mode != 'create') {                    
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.keyword = e.keyword
                    this.formData.company_id = e.car_company.id
                    this.formData.category_id = e.car_category.id
                    this.formData.engine_id = e.car_engine.id
                    this.formData.num_pax = e.num_pax
                    this.formData.price_from = e.price_from
                    this.formData.is_electric = e.is_electric
                    this.formData.tanque = e.tanque
                    this.formData.active = e.active
                    if(e.images)    
                        this.initGallery(e.images)
                }
            }).catch(err => {
                this.$router.push({name:'Login'})  
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias) && this.validate(this.formData.keyword) 
            && this.validate(this.formData.category_id) && this.validate(this.formData.engine_id)
            && this.validate(this.formData.company_id)) {   
                this.load = true

                const data = {                    
                    alias: this.formData.alias,
                    keyword: this.formData.keyword,
                    car_company_id: (typeof this.formData.company_id == 'object') ? this.formData.company_id.value : this.formData.company_id,
                    car_category_id: (typeof this.formData.category_id == 'object') ? this.formData.category_id.value : this.formData.category_id,
                    car_engine_id: (typeof this.formData.engine_id == 'object') ? this.formData.engine_id.value : this.formData.engine_id,
                    num_pax: this.formData.num_pax,
                    price_from: this.formData.price_from,
                    is_electric: (this.formData.is_electric === true) ? 1 : 0,
                    tanque: this.formData.tanque,
                    active: (this.formData.active === true) ? 1 : 0,
                }
                if('id' in this.formData) data.id = this.formData.id

                const fd = new FormData()      
                
                if(this.img.url.includes('blob'))
                    fd.append('images', this.img.file, this.img.file.name)
                else 
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                // agrego los otros campos
                
                for(let field of Object.keys(data)) {
                    fd.append(field, data[field])
                }
                CarServices.addUpdCar({form: fd, id: data.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    this.clearForm()
                    if(!res) {                         
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el carro'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);                                                
                    } else {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }            
        },
        updImage(obj) {
            if(obj.index >= 0) {
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },
        clearForm() {
            this.formData = {                
                alias: '',
                keyword: '',
                company_id: {
                    value: 0,
                    text: ''
                },
                category_id: {
                    value: 0,
                    text: ''
                },
                engine_id: {
                    value: 0,
                    text: ''
                },
                is_electric: false,
                tanque: 60,
                num_pax: 4,
                price_from: 0,
                active: 1,
            }
        },
    },
}
</script>