<template lang="">
   <div>   
    <div id="map" style="height: 400px;"></div>            
   </div>
</template>
<script>

import L from 'leaflet';
import 'leaflet-draw';
import 'leaflet-draw/dist/leaflet.draw.css'
import 'leaflet/dist/leaflet.css'
// CSS
//https://leafletjs.com/reference.html
//https://leaflet.github.io/Leaflet.draw/docs
export default {  
   name: 'MapZone',
   props: {
     isNew: {
       type: Boolean,
       default: false
     },     
     latlng: {
       type: Array,
       default: [21, 77]
     }
   },
   data() {
       return {            
            zone: () => [],
            map: {}
        }
   },
   components: {
   },
   mounted() {
    const osmUrl = 'http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
        osmAttrib = '&copy; <a href="http://openstreetmap.org/copyright">OpenStreetMap</a> contributors',
        osm = L.tileLayer(osmUrl, { maxZoom: 18, attribution: osmAttrib })

        this.map = new L.Map('map', {layers: [osm], center: new L.LatLng(0,0), zoom: 16 });

        this.map.setView(new L.LatLng(23.139422,-82.382617))
        var drawnItems = new L.FeatureGroup();
        this.map.addLayer(drawnItems);

        // L.drawLocal.draw.toolbar.buttons.polygon = 'Draw a sexy polygon!';

        this.map.addControl(new L.Control.Draw({
            position: 'topright',
            edit: {
                featureGroup: drawnItems,
                poly: {
                    allowIntersection: false
                }
            },
            draw: {
                polyline: false,
                circle: false,
                marker: {
                    icon: L.divIcon({className: 'custom-icon'})
                },
                circlemarker: false,
                rectangle: false,
                polygon: false                
            }
        }));

        L.marker([23.139422,-82.38261], L.divIcon({className: 'custom-icon'})).addTo(this.map);

        this.map.on(L.Draw.Event.CREATED, function (event) {
            const layer = event.layer;
            console.log(event.layer._latlng)
            // salvando la zona creada
            this.zone = layer._latlngs
            // localStorage.setItem('ZONE',JSON.stringify(this.zone))
            drawnItems.addLayer(layer);
        });    

        this.map.on(L.Draw.Event.EDITED, function (event) {
            const layer = event.layer;
            
            // salvando la zona actualizada
            this.zone = layer._latlngs
            // localStorage.setItem('ZONE',JSON.stringify(this.zone))
            drawnItems.addLayer(layer);
        });    
   }, 
   created() {
    //    console.log(this.map)
    //    this.map.setView(new L.LatLng(this.latlng[0], this.latlng[1]))
   }      
}
</script>
<style>
.custom-icon {
    background-image: url('../../assets/img/location.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 20px !important;
    height: 20px !important;
}
</style>
