<template>
    <div>
        <form action="" class="mt-2" @submit="submitForm">
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Active</label>
                    <input v-model="formData.active" type="checkbox"
                        class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre *</label>
                    <input v-model="formData.alias" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword *</label>
                    <input v-model="formData.keyword" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Descripcin</label>
                    <input v-model="formData.description" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Categoras *</label>
                    <multi-select :options="categoriesSelected" :selected-options="formData.categories"
                        placeholder="Seleccione el estado" @select="onCategorySelect" required class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    </multi-select>
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">En oferta</label>
                    <input v-model="formData.in_offer" type="checkbox"
                        class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Entrega gratis</label>
                    <input v-model="formData.delivery_free" type="checkbox"
                        class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label
                        class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Descuento</label>
                    <input v-model="formData.discount" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Total en
                        almacen</label>
                    <input v-model="formData.stock" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">

                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Precio
                        *</label>
                    <input v-model="formData.price" type="text"
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label
                        class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Caractersticas</label>
                    <div
                        class="flex flex-wrap gap-3 w-full my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                        <button type="button"
                            class="mb-auto ml-auto border border-gray-400 bg-lightBlue rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm p-1 px-3"
                            @click="addFeature"><i class="fa fa-plus"> Agregar Caracterstica</i>
                        </button>
                        <Features v-for="(feature, index) in formData.features" :key="index" :info="feature"
                            :index="index" @delete="delFeature" @update="updFeature" />
                    </div>
                </div>

                <!-- Img -->
                <div class="flex flex-row items-center my-3 mx-2">
                    <label
                        class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Imagen</label>
                    <div class="flex-1">
                        <!-- Mensaje informativo sobre formato y tamao -->
                        <div class="mb-3 p-2 bg-blue-50 border border-blue-200 rounded-md text-sm text-blue-700">
                            <i class="fas fa-info-circle mr-2"></i>
                            Solo se permiten archivos JPG y PNG menores a 100KB
                        </div>

                        <add-images :index="1" :info="img" v-on:imgStatus="updImage" accept=".jpg,.png"
                            class="mt-1 block w-full bg-white focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                        </add-images>
                    </div>
                </div>

                <!-- Mensaje de error para imagen grande -->
                <div v-if="imageSizeError" class="flex flex-row items-center my-3 mx-2">
                    <div class="ml-32 w-full">
                        <div class="p-3 bg-red-100 border border-red-400 text-red-700 rounded">
                            <i class="fas fa-exclamation-triangle mr-2"></i>
                            La imagen es demasiado grande ({{ imageSizeError }}). El tamao mximo permitido es 300KB.
                        </div>
                    </div>
                </div>

            </div>

            <!-- Button -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition" :disabled="load || imageSizeError || imageTypeError"
                    @click="submitForm">
                    Salvar
                    <span v-if="load" class="flex items-center ml-2">
                        <i class="fa fa-spinner fa-pulse"></i>
                    </span>
                </button>
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition" :disabled="load" @click="cancelForm">Cancelar
                </button>
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ error_message_text }}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ success_message_text }}</h5>
                </div>
            </div>

        </form>
    </div>
</template>
<script>

import MarketplaceItemService from "@/services/marketplace/marketplace_item.service";
import MarketplaceCategoryService from "@/services/marketplace/marketplace_category.service";
import Features from "./partials/Features";
import Swal from 'sweetalert2';

import { MultiSelect } from 'vue-search-select';
import AddImages from '@/components/AddImages';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudMarketplaceItem",
    components: {
        AddImages,
        MultiSelect,
        Features,
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            categoriesSelected: [],
            load: false,
            img: {},
            features: [],
            imageSizeError: null,
            imageTypeError: null,
            formData: {
                //general info
                // id: '',
                alias: '',
                keyword: '',
                description: '',
                discount: 0,
                stock: 0,
                price: '',
                features: [],
                images: [],
                categories: [],
                active: true,
                in_offer: false,
                delivery_free: false,
            },
        }
    },
    created() {
        MarketplaceCategoryService.getSimpleList().then(item => { this.categoriesSelected = item })
        this.initForm()
    },
    watch: {
        mode() {
            this.initForm()
        },
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods: {
        initForm() {
            this.imageSizeError = null;
            this.imageTypeError = null;

            // For upd action
            MarketplaceItemService.getMarketplaceItem(this.info)
                .then(item => {
                    if (this.mode != 'create') {
                        this.formData.id = item.id
                        this.formData.alias = item.alias
                        this.formData.description = item.description
                        this.formData.keyword = item.keyword
                        this.formData.discount = item.dispo ? item.dispo.discount : 0
                        this.formData.stock = item.dispo ? item.dispo.stock : 0
                        this.formData.price = item.price
                        this.formData.features = item.features
                        this.formData.active = item.active
                        this.formData.in_offer = item.in_offer
                        this.formData.delivery_free = item.delivery_free
                        this.formData.categories = [];

                        // categorias
                        if (item.marketplace_categories.length > 0)
                            item.marketplace_categories.forEach(el => this.formData.categories.push(
                                {
                                    value: el.id,
                                    text: el.alias,
                                }
                            ))
                        // imagen
                        if (item.images)
                            this.initGallery(item.images)
                    }
                })
        },
        validate(value) {
            if (value == '')
                return false
            else if (value === undefined)
                return false
            else if (value === null)
                return false
            return true
        },
        updImage(obj) {
            if(obj.index >= 0) {
                this.imageSizeError = null;
                this.imageTypeError = null;
                
                // Validar tamao y tipo de la imagen antes de asignarla
                if (obj.img.file) {
                    const file = obj.img.file;
                    const fileSize = file.size;
                    const maxSize = 300 * 1024; // 300KB en bytes

                    // Solo permitir JPG con extensin .jpg
                    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png'];
                    const fileExtension = file.name.split('.').pop().toLowerCase();
                    
                    // Validar por tipo MIME y extensin - SOLO .jpg
                    if (!allowedTypes.includes(file.type) || !['jpg', 'png'].includes(fileExtension)) {
                        this.imageTypeError = 'Solo se permiten archivos JPG con extensin jpg y png.';
                        // Limpiar la imagen si el tipo es incorrecto
                        this.img = {};
                        return;
                    }

                    // Validar tamao
                    if (fileSize > maxSize) {
                        const sizeInKB = (fileSize / 1024).toFixed(2);
                        this.imageSizeError = `${sizeInKB} KB`;
                        // Limpiar la imagen si es demasiado grande
                        this.img = {};
                        return;
                    }
                }

                // Si pasa todas las validaciones, asignar la imagen
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },
        initGallery(images) {
            this.imageSizeError = null;
            this.imageTypeError = null;

            for (let img of images) {
                const original = img.resources.filter(i => i.image == 'original')
                this.img = {
                    id: img.id,
                    class_name: img.class_name,
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                    in_offer: img.in_offer,
                    delivery_free: img.delivery_free,
                }
            }

        },
        async submitForm(e) {
            e.preventDefault();
            this.error_message = false
            this.success_message = true

            // Validar si hay errores de imagen
            if (this.imageSizeError || this.imageTypeError) {
                this.error_message = true
                this.success_message = false
                if (this.imageTypeError) {
                    this.error_message_text = this.imageTypeError;
                } else {
                    this.error_message_text = 'La imagen es demasiado grande. Por favor, seleccione una imagen menor a 300KB.';
                }
                return;
            }

            if (!this.validate(this.formData.alias) ||
                !this.validate(this.formData.keyword) ||
                !this.validate(this.formData.price) ||
                !this.formData.categories.length > 0
            ) {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                return;
            }

            // Mostrar loading con SweetAlert2
            Swal.fire({
                title: 'Procesando...',
                html: 'Guardando la informacin del producto',
                allowOutsideClick: false,
                allowEscapeKey: false,
                allowEnterKey: false,
                showConfirmButton: false,
                willOpen: () => {
                    Swal.showLoading();
                }
            });

            this.load = true;

            const data = {
                alias: this.formData.alias,
                description: this.formData.description,
                active: (this.formData.active) ? 1 : 0,
                keyword: this.formData.keyword,
                discount: this.formData.discount ? this.formData.discount : 0,
                stock: this.formData.stock ? this.formData.stock : 0,
                price: this.formData.price,
                features: this.formData.features,
                in_offer: this.formData.in_offer ? 1 : 0,
                delivery_free: this.formData.delivery_free ? 1 : 0,
            }

            data.categories = this.formData.categories.map(el => el.value)

            // Unir las features en un solo texto
            data.features = data.features.map(el => el.title + ': ' + el.value).join('/*/')

            if ('id' in this.formData) data.id = this.formData.id
            const fd = new FormData()

            if (!!this.img.url && !this.imageSizeError) {
                if (this.img.url.includes('blob')) {
                    fd.append('images', this.img.file, this.img.file.name)
                } else
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                fd.append('subcategory', [this.img.subcategory])
            }

            for (let field of Object.keys(data)) {
                fd.append(field, data[field])
            }

            try {
                const res = await MarketplaceItemService.addUpdMarketplaceItem({ form: fd, id: data.id }, (this.mode === 'create'))

                // Cerrar loading
                Swal.close();

                if (res) {
                    // Mostrar mensaje de xito
                    await Swal.fire({
                        icon: 'success',
                        title: 'xito!',
                        text: this.mode === 'create' ? 'Producto creado correctamente' : 'Producto actualizado correctamente',
                        timer: 2000,
                        showConfirmButton: false
                    });

                    this.success_message = true;
                    this.success_message_text = this.mode === 'create' ? 'Producto creado correctamente' : 'Producto actualizado correctamente';

                    setTimeout(() => {
                        this.success_message = false;
                        this.initForm();
                        this.$emit('saved'); // Emitir evento para que el componente padre sepa que se guard
                    }, 2000);

                } else {
                    throw new Error('Error en la respuesta del servidor');
                }
            } catch (error) {
                // Cerrar loading y mostrar error
                Swal.close();

                await Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: this.mode === 'create' ? 'Error al crear el producto' : 'Error al actualizar el producto',
                    timer: 3000,
                    showConfirmButton: false
                });

                this.error_message = true;
                this.success_message = false;
                this.error_message_text = this.mode === 'create' ? 'Error al crear el producto' : 'Error al actualizar el producto';

                setTimeout(() => {
                    this.error_message = false;
                }, 3000);
            } finally {
                this.load = false;
            }
        },

        cancelForm() {
            Swal.fire({
                title: 'Ests seguro?',
                text: 'Los cambios no guardados se perdern',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'S, cancelar',
                cancelButtonText: 'No, continuar editando'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$emit('close');
                }
            });
        },

        async getMarketplaceItem(e) {
            console.log(e)
        },

        onCategorySelect(items, litem) {
            this.formData.categories = items
        },

        // Features
        addFeature() {
            this.formData.features.push({ title: '', value: '' })
        },

        delFeature(index) {
            this.formData.features.splice(index, 1)
        },

        updFeature({ index, title, value }) {
            this.formData.features[index] = { title, value }
        }
    }
}
</script>
<style></style>