import Authorization from "../user/auth.service";

const base_url = 'car-pickup-places'
const CountryService = {
    getList() {
        try {
            return Authorization.checkLogin(`/${base_url}`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            time: item.pickup_time +' - '+item.dropoff_time,                            
                            active: (item.active) ? true : false,
                        })                        
                    }                
                } else {                    
                    return {errors: res.data}
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/${base_url}`).then(res => {
                if (res.status == 200) {
                    const pickupPlaces = []
                    for (let pickupPlace of res.data) {
                        pickupPlaces.push({
                            value: pickupPlace.id,
                            text: pickupPlace.alias
                        })
                    }
                    return pickupPlaces
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    async getPickupPlace(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdPickupPlace(formData, is_new = true) {
        try {
            formData.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin(`/${base_url}/create`, formData, 'post'):
                await Authorization.checkLogin(`/${base_url}/upd/${formData.id}`, formData, 'put')
            if (status == 200) {
                return data
            } 
        } catch (e) {            
            console.log(e.response)
            return {error: e}
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default CountryService