import Authorization from "../user/auth.service";

const HotelChainService = {
    getList() {
        try {
            return Authorization.checkLogin(`/hotel-chain`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.alias
                })
            }
            return result
        })

    },
    async getHotelChain(id) { //! Obtiene los datos de un Hotel dado un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/hotel-chain/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdHotelChain(chain_data, is_new = true) {
        try {
            chain_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/hotel-chain/create', chain_data, 'post'):
                await Authorization.checkLogin(`/hotel-chain/upd/${chain_data.id}`, chain_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/hotel-chain/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default HotelChainService