<template >
    <div class="w-full bg-white mb-auto text-darkGray rounded-lg py-4
    md:w-1/5">        
        <button class="w-full flex flex-row justify-between items-center text-2xl px-3 pb-4"
        v-if="showAccordion"
        @click="isExpanded=!isExpanded">
            Admin Options
            <span v-show="isExpanded"><i class="fa fa-chevron-up"></i></span>
            <span v-show="!isExpanded"><i class="fa fa-chevron-down"></i></span>
        </button>
        <VueAccordion class="flex flex-col gap-y-3"
        :expanded="isExpanded">
        <ul>
            <li v-for="(item, index) in navbar" :key="index" class="w-full flex flex-col">
                <template v-if="(item.secure)">
                    <!-- Menu -->
                    <button class="w-full flex flex-row gap-2 text-gray-700 py-2 px-8 items-center
                    hover:bg-indigo-200 hover:text-gray-700"                
                    :class="{'text-indigo-400': item.active}"
                    @click="activeItemNavbar(index)">
                        <i class="fa" :class="'fa-'+item.icon"></i>
                        <span>{{ item.name }}</span>
                        <i v-if="item.submenu" class="fas fa-caret-right"  :class="{'fas fa-caret-down': item.activeSubmenu}" @click="!item.activeSubmenu"></i>
                    </button>  

                    <!-- SubMenu -->
                    <ul v-if="item.activeSubmenu" class="flex flex-col flex-1 py-2">
                        <li v-for="(it, id) in navbar[index].child" :key="id" class="flex flex-col flex-1">
                            <button @click="activeItemNavbar(index,id)"  
                            v-if="it.secure"                          
                            class="flex flex-row gap-3 text-gray-700 ml-10 py-2 px-2 items-center hover:bg-indigo-200 hover:text-gray-700">
                                <i v-show="(it.icon)" class="fa" :class="'fa-'+it.icon"></i>
                                <span>{{ it.name }}</span>
                            </button>
                        </li>
                    </ul>                
                </template>                

            </li>
        </ul>

        </VueAccordion>            
    </div>
</template>
<script>
