                            secure: Authorization.validateRole(['ROLE_Admin']),
                            service: Services.RolesService
                        }
                    ]
                },
                //Poles
                {
                    name: "Polos",
                    icon: 'street-view',
                    active: false,     
                    secure: Authorization.validateRole(['ROLE_Comercial']),                            
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Polo",
                        active: Authorization.validateRole(['ROLE_Comercial']),
                        keyword: "pole",
                        secure: true,
