                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        // service: Services.CarPriceService
                    }]
                },
                //Prices
                {
                    name: "Precios",
                    icon: 'money-bill',
                    active: false,    
                    secure: Authorization.validateRole(['ROLE_Prices']),                             
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Tipo de tarifa",
                        active: false,
