                        secure: Authorization.validateRole(['ROLE_Prices']),
                        // service: Services.HotelRoomPriceService
                    }]
                },
                //Galleries
                {
                    name: "Galeras",
                    icon: 'image',
                    active: false, 
                    secure: Authorization.validateRole(['ROLE_Comercial']),                   
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                            name: "Galera",
                            active: false,
                            keyword: "gallery",
                            secure: Authorization.validateRole(['ROLE_Comercial']),
                            service: Services.GalleryService
                        },{
                            name: "Galera de Filtro",
                            active: false,
                            keyword: "gallery_filter",
                            secure: Authorization.validateRole(['ROLE_Comercial']),
                            service: Services.GalleryFilterService
                        }
                    ]
                },//Booking
                {
                    name: "Reservas",
                    icon: 'fa fa-calendar',
                    active: false,    
                    secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Reservas",
                        active: false,
                        keyword: "booking",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.BookingService
                    },{
                        name: "Estados Booking",
                        active: false,
                        keyword: "booking_status",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.BookingStatusService
                    },
                    ]
                },//Marketplace
                {
                    name: "Mercado",
                    icon: 'fa fa-shopping-cart',
                    active: false,    
                    secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Productos",
                        active: false,
                        keyword: "marketplace_item",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.MarketplaceItemService
                    },{
                        name: "Ordenes",
                        active: false,
                        keyword: "marketplace_order",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.MarketplaceOrderService
                    },{
                        name: "Requerimientos de entrega",
                        active: false,
                        keyword: "marketplace_delivery",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.MarketplaceDeliveryService
                    },{
                        name: "Clientes marketplace",
                        active: false,
                        keyword: "marketplace_client",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.MarketplaceClientService
