                        service: Services.MarketplaceCategoryService
                    },{
                        name: "Provincias",
                        active: false,
                        keyword: "marketplace_province",
                        secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                        service: Services.MarketplaceProvinceService
                    },{
                        name: "Municipios",
                        active: false,
                        keyword: "marketplace_city",
