                    ]
                },
            ],            
        }
    },
    created() {
        // Actualizando el role del usuario
    },
    methods: {
        async activeItemNavbar(index,subindex=null){   
            let CrudKey, name, keyword, list, url
            if (!this.navbar[index].active) {
                //Show Submenu                                    
                if(this.navbar[index].submenu){
                    //desactive items submenu
                    this.navbar.map(e => {e.active = false})                                                
                    if(subindex != null){
                        //show content submenu                                                
                        this.navbar[index].child.map(e => {e.active = false})                        
                        this.navbar[index].child.active = true   
                        CrudKey = this.navbar[index].child[subindex].keyword
                        name = this.navbar[index].child[subindex].name
                        keyword = this.navbar[index].child[subindex].keyword
                        url = (this.navbar[index].child[subindex].url) ? this.navbar[index].child[subindex].url : 'CrudKey'
                        if(this.navbar[index].child[subindex].service) {
                            list = await this.navbar[index].child[subindex].service.getList()
                        }
                    } else {
                        //active and desactive submenu
                        this.navbar[index].activeSubmenu = !this.navbar[index].activeSubmenu                        
                    }
                } else {
                //Show Menu
                    CrudKey = this.navbar[index].keyword
                    name = this.navbar[index].name
                    keyword = this.navbar[index].keyword
                    list = await this.navbar[index].child[subindex].service.getList()
                }                                   
                if ((this.$route.fullPath != '/admin/prices/' + keyword && this.$route.fullPath != '/admin/' + keyword) && CrudKey) {
                    this.$router.push({ name: url, params: {CrudKey} })
                    this.$emit('setNavBar', name, keyword, list)
                }
                this.$emit('crudStatus')
            }
        },  
    },
}
</script>
