import Authorization from "../user/auth.service";

const base_url = "offer";
const OffersService = {
  getList() {
    try {
      return Authorization.checkLogin(`/offers`).then((res) => {
        const list = [];
        if (res.status == 200) {
          for (let item of res.data) {
            list.push({
              id: item.id,
              title: item.title,
              link: item.is_link ? true : false,
              keyword: item.keyword,
              description: item.description,
            });
          }
        } //! Fail check login
        else {
          return {
            success: false,
            errors: "Fail check login",
          };
        }
        return list;
      });
    } catch (e) {
      console.log(e);
    }
  },
  async getOffer(id) {
    try {
      const { status, data } = await Authorization.checkLogin(`/offer/${id}`);
      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.log(e.response);
    }
  },
  async addUpdOffer(params, is_new = true /* , hasImage = false */) {
    try {
      params.auth = "bearer";
      // const url = (is_new) ? `/${base_url}/create` : `/${base_url}/upd/${params.id}`

      const { status, data } = is_new
        ? await Authorization.checkLoginImage(`/${base_url}/create`, params.form, "post")
        : await Authorization.checkLoginImage(`/${base_url}/upd/${params.id}`, params.form, "post");

      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.log(e);
    }
    return false;
  },
  async delete(ids) {
    const delete_data = {
      auth: "bearer",
      ids,
    };
    try {
      const { status, data } = await Authorization.checkLogin("/offers/delete", delete_data, "post");
      if (status == 200) {
        return this.getList().then((list) => {
          return list;
        });
      }
    } catch (e) {
      //! Fail
      console.log(e.response);
    }
  },
};

export default OffersService;
