import Authorization from "../user/auth.service";

const base_url = "marketplace-delivery";
const MarketplaceDeliveryService = {
  getList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {

            const option = {
              id: item.id,
              province: item.marketplace_province.name,
              max_items: item.max_items,
              price: item.price,
            };

            list.push(option);
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },
  getSimpleList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {
            if (item.id !== 1) {
              const option = {
                value: item.id,
                text: item.alias,
              };

              list.push(option);
            }
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },

  async getMarketplaceDelivery(id) {
    //! Obtiene los datos x un ID
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`, { auth: "bearer" });
      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
    }
  },

  async addUpdateMarketplaceDelivery(form_data, is_new = true) {  
    try {
      
      form_data.auth = 'bearer'
      
      const { status, data } = (is_new) ?
      await Authorization.checkLogin('/marketplace-delivery/create', form_data, 'post'):
          await Authorization.checkLogin(`/marketplace-delivery/upd/${form_data.id}`, form_data, 'put')
      
      if (status === 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
    }
    return false;
  },

  async delete(ids) {
    const delete_data = {
      auth: "bearer",
      ids,
    };
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, "post");
      if (status == 200) {
        return this.getList().then((list) => {
          return list;
        });
      }
    } catch (e) {
      //! Fail
      console.error(e.response);
    }
  },


};

export default MarketplaceDeliveryService;
