import Authorization from "../user/auth.service";

const RateTypeService = {
    getList(product_type=null) {
        product_type = (!product_type) ? {product_type} : product_type 
        try {
            return Authorization.checkLogin(`/rate-type`, product_type, 'post').then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            name: item.name,
                            product: item.product_type.alias,
                            active: (item.active) ? true : false
                        })
                    }
                } else { //! Fail check login
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList(product_type=null) {
        return this.getList(product_type).then(list => {
            const result = []            
            for (let l of list) {
                result.push({
                    value: l.id,
                    text: l.name
                })
            }
            return result
        })

    },
    async getRateType(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/rate-type/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdRateType(rate_data, is_new = true) {
        try {
            //console.log(rate_data)
            rate_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/rate-type/create', rate_data, 'post'):
                await Authorization.checkLogin(`/rate-type/upd/${rate_data.id}`, rate_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/rate-type/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default RateTypeService